import { Check, Minus, Plus } from 'lucide-react';
import { Select } from '../../ui/select';
import { Switch } from '../../ui/switch';
import { Slider } from '../../ui/slider';
import { Label } from '../../ui/label';
import { Button } from '../../ui/button';
import { PageOption } from './page-editor';
import { PageItem } from './page-group-utils';

type PageOptionsRendererProps = {
  pageOptions: PageOption[];
  targetItem: PageItem;
  onChange: (pageOption: PageOption, targetItem: PageItem, value: string) => void;
};

export function PageOptionsRenderer({
  pageOptions,
  targetItem,
  onChange,
}: PageOptionsRendererProps) {
  const matchesTarget = (option: PageOption, item: PageItem) => {
    if (!option.appliesTo) return true;
    const targets = Array.isArray(option.appliesTo) ? option.appliesTo : [option.appliesTo];
    return targets.some(target => {
      if (typeof target === 'function') return target(item);
      // Match by instance ID, template ID, or component key
      return target === item.id || target === item.templateId || (item as any).componentKey === target;
    });
  };

  // Filter out options that don't apply to this page (getValue returns empty string or false for non-applicable options)
  const applicableOptions = pageOptions.filter(option => {
    if (!matchesTarget(option, targetItem)) return false;
    const value = option.getValue(targetItem);
    if (option.type === 'select' || option.type === 'color-series') {
      return value !== ''; // Only show if value is not empty
    }
    if (option.type === 'toggle') {
      // Always show toggles, they can be false
      return true;
    }
    // For slider and counter, always show
    return true;
  });

  const renderOption = (option: PageOption) => {
    const currentValue = option.getValue(targetItem);

    switch (option.type) {
      case 'select': {
        return (
          <div key={option.id} className="space-y-1.5">
            <Label htmlFor={option.id} className="text-xs font-medium text-gray-500">
              {option.label}
            </Label>
            <Select
              id={option.id}
              value={String(currentValue)}
              onChange={(e) => onChange(option, targetItem, e.target.value)}
              className="w-full text-sm"
            >
              {option.options.map((opt) => (
                <option key={opt.value} value={opt.value}>
                  {opt.label}
                </option>
              ))}
            </Select>
          </div>
        );
      }

      case 'toggle': {
        const boolValue = typeof currentValue === 'boolean' ? currentValue : currentValue === 'true';
        return (
          <div key={option.id} className="flex items-center justify-between py-1.5">
            <Label htmlFor={option.id} className="text-xs font-medium text-gray-500">
              {option.label}
            </Label>
            <Switch
              id={option.id}
              checked={boolValue}
              onCheckedChange={(checked) => onChange(option, targetItem, String(checked))}
            />
          </div>
        );
      }

      case 'slider': {
        const numValue = typeof currentValue === 'number' ? currentValue : Number(currentValue) || option.min;
        return (
          <div key={option.id} className="space-y-1.5">
            <div className="flex items-center justify-between">
              <Label htmlFor={option.id} className="text-xs font-medium text-gray-500">
                {option.label}
              </Label>
              <span className="text-xs font-mono tabular-nums text-gray-700">{numValue}</span>
            </div>
            <Slider
              id={option.id}
              min={option.min}
              max={option.max}
              step={option.step}
              value={[numValue]}
              onValueChange={(values) => onChange(option, targetItem, String(values[0]))}
            />
          </div>
        );
      }

      case 'counter': {
        const numValue = typeof currentValue === 'number' ? currentValue : Number(currentValue) || option.min;
        return (
          <div key={option.id} className="space-y-1.5">
            <Label className="text-xs font-medium text-gray-500">{option.label}</Label>
            <div className="flex items-center gap-2">
              <Button
                variant="outline"
                size="sm"
                className="h-8 w-8 shrink-0 p-0"
                onClick={() => {
                  const newValue = Math.max(option.min, numValue - option.step);
                  onChange(option, targetItem, String(newValue));
                }}
                disabled={numValue <= option.min}
                type="button"
              >
                <Minus className="h-3.5 w-3.5" />
              </Button>
              <div className="flex-1 text-center px-3 py-1.5 bg-gray-50 rounded-md border border-gray-200">
                <span className="text-sm font-mono tabular-nums font-medium text-gray-900">{numValue}</span>
              </div>
              <Button
                variant="outline"
                size="sm"
                className="h-8 w-8 shrink-0 p-0"
                onClick={() => {
                  const newValue = Math.min(option.max, numValue + option.step);
                  onChange(option, targetItem, String(newValue));
                }}
                disabled={numValue >= option.max}
                type="button"
              >
                <Plus className="h-3.5 w-3.5" />
              </Button>
            </div>
          </div>
        );
      }

      case 'color-series': {
        const stringValue = String(currentValue);
        return (
          <div key={option.id} className="space-y-1.5">
            <Label className="text-xs font-medium text-gray-500">{option.label}</Label>
            <div className="flex flex-wrap gap-1.5">
              {option.options.map((color) => {
                const isSelected = stringValue === color.value;
                return (
                  <button
                    key={color.value}
                    onClick={() => onChange(option, targetItem, color.value)}
                    className={`h-7 w-7 rounded-md border-2 transition-all flex items-center justify-center ${
                      isSelected
                        ? 'border-gray-900 scale-110'
                        : 'border-gray-200 hover:border-gray-400 hover:scale-105'
                    }`}
                    style={{ backgroundColor: color.hex || color.value }}
                    type="button"
                    title={`${color.label}${color.hex ? ` (${color.hex})` : ''}`}
                  >
                    {isSelected && (
                      <Check className="h-4 w-4 text-white drop-shadow-[0_1px_2px_rgba(0,0,0,0.8)]" strokeWidth={3} />
                    )}
                  </button>
                );
              })}
            </div>
          </div>
        );
      }

      default:
        console.warn(`Unknown option type: ${(option as any).type}`);
        return null;
    }
  };

  return <div className="space-y-3">{applicableOptions.map((option) => renderOption(option))}</div>;
}
