/**
 * Integration Data Utilities
 * 
 * Essential utilities for working with integration data in multi-instance templates.
 */

import type { PageItem } from './page-group-utils';
import { isPageGroup } from './page-group-utils';

/**
 * Get integration data for a specific page/group instance
 */
function getIntegrationData(payload: any, instanceId: string): any {
  if (!payload?.integrations) return undefined;
  return payload.integrations[instanceId];
}

export function resolveIntegrationInstanceId(
  page: { id: string },
  parentGroup?: PageItem
): string | null {
  if (parentGroup && isPageGroup(parentGroup)) {
    return parentGroup.id;
  }
  return page?.id ?? null;
}

export function resolveIntegrationBinding(
  payload: any,
  page: { id: string },
  parentGroup?: PageItem
): { instanceId: string | null; integration?: any } {
  const instanceId = resolveIntegrationInstanceId(page, parentGroup);
  if (!instanceId) return { instanceId: null, integration: undefined };
  return {
    instanceId,
    integration: getIntegrationData(payload, instanceId),
  };
}

/**
 * Resolve integration data for a page component
 * For group pages, prefer parent group's integration data
 */
export function resolveIntegrationForPage(
  payload: any,
  page: { id: string },
  parentGroup?: PageItem
): any {
  const binding = resolveIntegrationBinding(payload, page, parentGroup);
  return binding.integration;
}

export function buildIntegrationPath(
  instanceId?: string | null,
  fieldPath?: string
) {
  if (!instanceId) return null;
  const base = `integrations.${instanceId}`;
  if (!fieldPath) return base;
  return `${base}.${fieldPath}`;
}

/**
 * Parse a dialog path to determine if it targets integration data
 * Returns the instance ID if the path targets integrations, null otherwise
 */
export function parseIntegrationPath(path: string): {
  instanceId: string | null;
  fieldPath: string;
  isIntegrationPath: boolean;
} {
  if (!path) {
    return { instanceId: null, fieldPath: path, isIntegrationPath: false };
  }
  
  // Check if path starts with "integrations."
  const integrationPrefix = 'integrations.';
  if (path.startsWith(integrationPrefix)) {
    const afterPrefix = path.slice(integrationPrefix.length);
    const dotIndex = afterPrefix.indexOf('.');
    
    if (dotIndex > 0) {
      // Path like "integrations.group_1__123.field.subfield"
      const instanceId = afterPrefix.slice(0, dotIndex);
      const fieldPath = afterPrefix.slice(dotIndex + 1);
      return { instanceId, fieldPath, isIntegrationPath: true };
    } else {
      // Path like "integrations.group_1__123" (targets entire integration)
      const instanceId = afterPrefix;
      return { instanceId, fieldPath: '', isIntegrationPath: true };
    }
  }
  
  return { instanceId: null, fieldPath: path, isIntegrationPath: false };
}

/**
 * Set a nested value in an object using dot notation path
 */
function setNestedValue(obj: any, path: string, value: any): any {
  if (!path) return value;
  
  const keys = path.split('.');
  const result = { ...obj };
  let current: any = result;
  
  for (let i = 0; i < keys.length - 1; i++) {
    const key = keys[i];
    if (!(key in current) || typeof current[key] !== 'object' || current[key] === null) {
      current[key] = {};
    } else {
      current[key] = { ...current[key] };
    }
    current = current[key];
  }
  
  const lastKey = keys[keys.length - 1];
  current[lastKey] = value;
  
  return result;
}

/**
 * Update integration data in payload using a path-based update
 * Handles nested field updates (e.g., "integrations.id.overrides.heroImage.url")
 */
export function updateIntegrationByPath(
  payload: any,
  path: string,
  value: any
): any {
  const parsed = parseIntegrationPath(path);
  
  if (!parsed.isIntegrationPath || !parsed.instanceId) {
    // Not an integration path, return payload as-is
    return payload;
  }
  
  const { instanceId, fieldPath } = parsed;
  const currentIntegration = getIntegrationData(payload, instanceId) || {};
  
  // Update nested field using dot notation
  const updatedIntegration = setNestedValue(
    currentIntegration,
    fieldPath,
    value
  );
  
  return {
    ...payload,
    integrations: {
      ...(payload?.integrations || {}),
      [instanceId]: updatedIntegration,
    },
  };
}
