import * as React from 'react';
import { Lock } from 'lucide-react';
import { ReorderItem } from './page-group-utils';
import type { PageComponent } from './page-group-utils';
import { getPagePayload } from './template-data-provider';
import { resolveIntegrationForPage } from './integration-utils';

interface DefaultRenderThumbnailOptions {
  // PageComponent registry: map of pageId to React component
  pageComponents: { [pageId: string]: PageComponent };
  
  // Payload to pass to PageComponents
  payload?: any;
  
  // Page setup dimensions (same as Pagination setup prop) - in millimeters
  setup?: { width: number; height: number };
  
  // Optional: Custom thumbnail dimensions (height auto-derives from page aspect if omitted)
  thumbnailWidth?: number;
  thumbnailHeight?: number;
}

/**
 * Creates a default renderThumbnail function that renders actual PageComponents
 * instead of reference images. This ensures thumbnails accurately represent
 * the rendered page content.
 * 
 * @internal - This is an internal utility, not part of the public API
 */
export function createDefaultRenderThumbnail(
  options: DefaultRenderThumbnailOptions
): (item: ReorderItem, index: number, isDragging?: boolean) => React.ReactNode {
  const { 
    pageComponents, 
    payload, 
    setup = { width: 210, height: 297 }, // Default A4 size in mm
    thumbnailWidth = 200, 
    thumbnailHeight, 
  } = options;
  
  const pageWidthMm = setup.width || 210;
  const pageHeightMm = setup.height || 297;
  const pageAspectRatio = pageWidthMm / pageHeightMm;

  // Derive thumbnail height from page aspect ratio if not provided
  const finalThumbnailWidth = thumbnailWidth;
  const finalThumbnailHeight = thumbnailHeight ?? Math.round(finalThumbnailWidth / pageAspectRatio);

  // Convert mm to pixels at 96dpi (1mm = 3.779527559px) for accurate scaling
  const pageWidthPx = pageWidthMm * 3.779527559;
  const pageHeightPx = pageHeightMm * 3.779527559;

  
  return (item: ReorderItem, index: number, isDragging?: boolean) => {
    const strictPosition = (item as any).strictPosition;
    const isLocked = strictPosition === 'start' || strictPosition === 'end';
    
    if (item.kind === 'group') {
      // Group thumbnail: show first page with group indicator
      const firstPageId = item.firstPageId as string | undefined;
      const firstPageComponentKey = item.firstPageComponentKey ?? firstPageId;
      const firstPagePayload = getPagePayload(payload, { id: firstPageId, componentKey: firstPageComponentKey });
      // Use component from item if available, otherwise fall back to pageComponents registry
      const FirstPageComponent = item.firstPageComponent || (firstPageComponentKey ? pageComponents[firstPageComponentKey] : null);
      
      // Resolve integration data for group (group pages use the group's integration)
      // For groups, instanceId is the group ID, so we can directly access it
      const integration = payload?.integrations?.[item.id];      
      
      return (
        <div 
          className={`relative bg-white border-2 rounded-md overflow-hidden transition-all ${
            isDragging 
              ? 'border-blue-400 shadow-2xl scale-105' 
              : isLocked
              ? 'border-gray-300 bg-gray-50'
              : 'border-blue-300 hover:border-blue-400 hover:shadow-lg'
          }`}
          style={{ width: `${finalThumbnailWidth}px`, height: `${finalThumbnailHeight}px` }}
        >
          {/* Render actual PageComponent */}
          {FirstPageComponent ? (
            <div 
              className="w-full h-full flex items-center justify-center bg-gray-50 overflow-hidden relative pointer-events-none"
            >
              <div 
                className="absolute w-full h-full flex items-center justify-center pointer-events-none"
                style={{
                  transform: `scale(${Math.min(finalThumbnailWidth / pageWidthPx, finalThumbnailHeight / pageHeightPx)})`,
                  transformOrigin: 'center',
                }}
              >
                <div className="!shrink-0" style={{ width: `${pageWidthPx}px`, height: `${pageHeightPx}px`, backgroundColor: 'white', pointerEvents: 'none' }}>
                  <FirstPageComponent
                    payload={payload}
                    pageId={firstPageId}
                    templateId={firstPageComponentKey}
                    pagePayload={firstPagePayload}
                    componentKey={firstPageComponentKey}
                    integration={integration}
                  />
                </div>
              </div>
            </div>
          ) : (
            <div className="w-full h-full flex items-center justify-center bg-gray-50 pointer-events-none">
              <div className="text-center p-4">
                <div className="text-sm font-medium text-gray-700">Group {item.id}</div>
                <div className="text-xs text-gray-500 mt-1">{firstPageId || 'No preview'}</div>
              </div>
            </div>
          )}
          
          {/* Group indicator badge */}
          <div className="absolute top-2 right-2 px-2 py-1 bg-blue-600/80 backdrop-blur-sm text-white text-xs font-medium rounded shadow-lg pointer-events-none">
            Group ({item.pageCount} pages)
          </div>
          
          {/* Lock indicator for strict position */}
          {isLocked && (
            <div className="absolute top-2 left-2 px-2 py-1 bg-gray-600/80 backdrop-blur-sm text-white text-xs font-medium rounded shadow-lg pointer-events-none flex items-center gap-1">
              <Lock className="size-3" />
              <span>{strictPosition === 'start' ? 'Start' : 'End'}</span>
            </div>
          )}
          
          {/* Group info overlay */}
          <div className="absolute bottom-0 left-0 right-0 bg-black/40 backdrop-blur-sm p-3 pointer-events-none">
            <div className="flex items-center justify-between gap-2 text-white">
              <div className="flex-1 min-w-0">
                <div className="text-sm font-medium truncate">
                  {item.label || item.id}
                </div>
                <div className="text-xs opacity-90 truncate">{item.id}</div>
              </div>
              <div className="px-2 py-1 bg-blue-600/80 backdrop-blur-sm rounded text-xs shrink-0 font-medium">
                Group
              </div>
            </div>
          </div>
          
          {/* Drag indicator */}
          {isDragging && (
            <div className="absolute inset-0 flex items-center justify-center bg-blue-500/10 pointer-events-none">
              <div className="text-blue-600 font-medium text-sm bg-white/90 px-3 py-1 rounded-full shadow-lg">
                Dragging Group...
              </div>
            </div>
          )}
        </div>
      );
    } else {
      // Standalone page thumbnail
      const pageId = item.pageId as string | undefined;
      const pageComponentKey = item.pageComponentKey ?? pageId;
      const pagePayload = getPagePayload(payload, { id: pageId, componentKey: pageComponentKey });
      // Use component from item if available, otherwise fall back to pageComponents registry
      const PageComponent = item.pageComponent || (pageComponentKey ? pageComponents[pageComponentKey] : null);
      
      // Resolve integration data for standalone page
      const integration = pageId ? resolveIntegrationForPage(payload, { id: pageId }) : undefined;
      return (
        <div 
          className={`relative bg-white border-2 rounded-md overflow-hidden transition-all ${
            isDragging 
              ? 'border-blue-400 shadow-2xl scale-105' 
              : isLocked
              ? 'border-gray-300 bg-gray-50'
              : 'border-gray-200 hover:border-blue-300 hover:shadow-lg'
          }`}
          style={{ width: `${finalThumbnailWidth}px`, height: `${finalThumbnailHeight}px` }}
        >
          {/* Render actual PageComponent */}
          {PageComponent ? (
            <div 
              className="w-full h-full flex items-center justify-center bg-gray-50 overflow-hidden relative pointer-events-none"
            >
              <div 
                className="absolute inset-0 flex items-center justify-center pointer-events-none"
                style={{
                  transform: `scale(${Math.min(finalThumbnailWidth / pageWidthPx, finalThumbnailHeight / pageHeightPx)})`,
                  transformOrigin: 'center',
                }}
              >
                <div className="!shrink-0" style={{ width: `${pageWidthPx}px`, height: `${pageHeightPx}px`, backgroundColor: 'white', pointerEvents: 'none' }}>
                  <PageComponent 
                    payload={payload}
                    pageId={pageId}
                    templateId={pageComponentKey}
                    pagePayload={pagePayload}
                    componentKey={pageComponentKey}
                    integration={integration}
                  />
                </div>
              </div>
            </div>
          ) : (
            <div className="w-full h-full flex items-center justify-center bg-gray-50 pointer-events-none">
              <div className="text-center p-4">
                <div className="text-sm font-medium text-gray-700">Page {item.pageNum}</div>
                <div className="text-xs text-gray-500 mt-1">{pageId || 'No preview'}</div>
              </div>
            </div>
          )}
          
          {/* Lock indicator for strict position */}
          {isLocked && (
            <div className="absolute top-2 left-2 px-2 py-1 bg-gray-600/80 backdrop-blur-sm text-white text-xs font-medium rounded shadow-lg pointer-events-none flex items-center gap-1">
              <Lock className="size-3" />
              <span>{strictPosition === 'start' ? 'Start' : 'End'}</span>
            </div>
          )}
          
          {/* Page info overlay */}
          <div className="absolute bottom-0 left-0 right-0 bg-black/40 backdrop-blur-sm p-3 pointer-events-none">
            <div className="flex items-center justify-between gap-2 text-white">
              <div className="flex-1 min-w-0">
                <div className="text-sm font-medium truncate">
                  {item.pageLabel || `Page ${item.pageNum}`}
                </div>
                <div className="text-xs opacity-90 truncate">{item.pageId}</div>
              </div>
              <div className="px-2 py-1 bg-white/20 backdrop-blur-sm rounded text-xs shrink-0">
                Page
              </div>
            </div>
          </div>
          
          {/* Drag indicator */}
          {isDragging && (
            <div className="absolute inset-0 flex items-center justify-center bg-blue-500/10 pointer-events-none">
              <div className="text-blue-600 font-medium text-sm bg-white/90 px-3 py-1 rounded-full shadow-lg">
                Dragging...
              </div>
            </div>
          )}
        </div>
      );
    }
  };
}
