import { useState, useRef, useEffect } from 'react';
import { Previewer } from 'pagedjs';
import debounce from 'lodash/debounce';
import DocumentUtils from './pagination/DocumentUtils';
import PageSizeUtils from '../utility/PageSizeUtils';
import ConfigProvider from '../config/config-provider';
import './pagination/pagination-dynamic.css';
import './../styles/uhuu-indicators.css';

interface PaginationProps {
  children: React.ReactNode;
  className?: string;
  setup: any;  
}

const Pagination = ({ children, className, setup }: PaginationProps) => {
  
  const [rendering, setRendering] = useState(false);
  const containerRef = useRef(null);
  const contentRef = useRef(null);    
  const documentUtilsRef = useRef<DocumentUtils>(new DocumentUtils());

  const config = PageSizeUtils.pageParams("dynamic", setup);

  let printCssRaw = config?.page?.printCssRaw;
  if (config?.page?.width && config?.page?.height) {
    const pageRuleRegex = /@page\s*{([^}]*)}/;
    const match = printCssRaw?.match(pageRuleRegex);
    
    const sizeAndBleedStyles = `
      size: ${config.page.width}mm ${config.page.height}mm;
      bleed: ${config.page.bleed ?? 0}mm;`;

    if (match) {
      // Remove any existing size/bleed properties
      const existingStyles = match[1].replace(/\s*(size|bleed)\s*:[^;]*;/g, '');
      const newPageRule = `@page {${sizeAndBleedStyles}${existingStyles}}`;
      printCssRaw = printCssRaw.replace(pageRuleRegex, newPageRule);
    } else {
      printCssRaw = `@page {${sizeAndBleedStyles}}\n${printCssRaw ?? ''}`;
    }
  }

  const layoutPage = debounce(() => {
    if (!containerRef.current || !contentRef.current) {
      console.warn("Container or page content reference is missing.");
      return;
    }

    if(rendering) {
      return;
    }
    
    setRendering(true);    
    documentUtilsRef.current.prepareRendering(containerRef.current);

    const paged = new Previewer();
    const styleUrls = documentUtilsRef.current.getStyleUrls(printCssRaw ?? '', config?.page?.printCssUrl ?? '');
    
    try {
      paged.preview(contentRef.current?.innerHTML ?? '', styleUrls, containerRef.current).then(() => {        
        setRendering(false);
        documentUtilsRef.current.finalizeRendering(containerRef.current);
      });
    } catch (error) {
      console.error("Error during Paged.js preview rendering:", error);      
      setRendering(false);
    }
  }, 500, { leading: true });

  useEffect(() => {
    layoutPage();
  }, [children, setup]);

  return (
    <ConfigProvider config={config}>
      <div className={[(className ?? ''), (rendering ? 'rendering' : 'rendered'), (config?.page?.showBleed ? 'uhuu-bleed-visible' : ''), (config?.page?.preview ?? '')].filter(Boolean).join(' ')} ref={containerRef}></div>
      <div className="uhuu-reactive-content" style={{display: 'none'}} ref={contentRef}>
        {children}
      </div>
    </ConfigProvider>
  );
};

export default Pagination;