declare const $uhuu: {
  editDialog: (value: any) => void;
};

class DocumentUtils {
  prepareRendering(container: HTMLElement) {        
    const { clientHeight: height, clientWidth: width } = container;
    container.innerHTML = '';
    if (height) container.style.minHeight = `${height}px`;
    if (width && window !== window.parent) container.style.minWidth = `${width}px`;
    document?.querySelectorAll("style[data-pagedjs-inserted-styles]").forEach((style) => style.remove());
    this.resetZoom();
  }

  finalizeRendering(container: HTMLElement) {    
    if (container) {
      container.style.minWidth = 'auto';
      container.style.minHeight = 'auto';
    }
    this.attachEventListeners();
    this.applyPagedCSS();
    this.restoreZoom();
  }

  attachEventListeners() {        
    document?.querySelectorAll("[data-uhuu]").forEach((item) => {
      const clickListener = function () {
        const value = JSON.parse(this.getAttribute("data-uhuu"));
        $uhuu.editDialog(value);
      };
      item.removeEventListener('click', clickListener);
      item.addEventListener('click', clickListener);
    });
  }

  applyPagedCSS() {    
    document?.querySelectorAll(".page-break-after[data-paged-css]").forEach((el) => {
      const pagedSheet = el.closest('div.pagedjs_sheet');
      const pagedCSS = el.getAttribute('data-paged-css');
      if (pagedSheet && pagedCSS) {
        pagedCSS.split(' ').filter(Boolean).forEach((cssClass) => pagedSheet.classList.add(cssClass));
      }
    });
  }

  getStyleUrls(styles: string, fileName: string) {
    if (styles) {
      const blob = new Blob([styles], { type: 'text/css' });
      return [URL.createObjectURL(blob)];
    }
    return fileName ? [fileName] : [];
  };

  private originalStyles: {
    width: string;
    height: string; 
    transform: string;
    transformOrigin: string;
  };

  resetZoom() {
    if (typeof document === 'undefined') return;
    
    const body = document?.body;
    if(!body.style.transform) {      
      return;     
    }
    
    this.originalStyles = {
      width: body.style.width,
      height: body.style.height,
      transform: body.style.transform,
      transformOrigin: body.style.transformOrigin || 'center center'
    };

    body.style.opacity = '0';
    body.style.width = '100%';
    body.style.height = '100%';
    body.style.transform = '';
  }

  restoreZoom() {           
    if (typeof document === 'undefined') return;
    
    const body = document?.body;
    body.style.opacity = '1';

    if(!this.originalStyles?.transform) return;            
    body.style.width = this.originalStyles.width;
    body.style.height = this.originalStyles.height;
    body.style.transform = this.originalStyles.transform;    
  }
}

export default DocumentUtils;