import * as React from "react"
import { GripVertical, ArrowUpDown, Lock } from "lucide-react"
import DragDropGrid from "../../utility/drag-drop-grid"
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetDescription, SheetFooter } from "../../ui/sheet"
import { Badge } from "../../ui/badge"
import { Button } from "../../ui/button"
import { createDefaultRenderThumbnail } from "./default-render-thumbnail"
import type { PageComponent } from "./page-group-utils"

export interface PageItem {
  id: string
  [key: string]: any
}

export interface PageOrderDialogProps<T extends PageItem> {
  open: boolean
  onOpenChange: (open: boolean) => void
  pages: T[]
  onReorder: (newOrder: T[]) => void
  renderThumbnail?: (page: T, index: number, isDragging?: boolean) => React.ReactNode
  
  // NEW: For automatic thumbnail rendering
  pageComponents?: { [pageId: string]: PageComponent }
  payload?: any
  
  // Page setup dimensions (same as Pagination setup prop) - in millimeters
  setup?: { width: number; height: number }
  
  title?: string
  description?: string
  gridColsClass?: string
}

// Default page thumbnail component with simple preview
function DefaultPageThumbnail<T extends PageItem>({
  page,
  index,
  isDragging,
}: {
  page: T
  index: number
  isDragging?: boolean
}) {
  const thumbnailWidth = 200
  const thumbnailHeight = 280
  const strictPosition = (page as any).strictPosition
  const isLocked = strictPosition === 'start' || strictPosition === 'end'

  return (
    <div
      className={`flex items-center justify-center border relative rounded-lg bg-white overflow-hidden transition-all ${
        isDragging
          ? "opacity-50 border-gray-400 shadow-xl scale-105"
          : isLocked
          ? "border-gray-300 bg-gray-50"
          : "border-gray-200 group-hover/drag-item:border-gray-300 group-hover/drag-item:shadow-md"
      }`}
    >      
      <div
        className="flex items-center justify-center"
        style={{
          width: `${thumbnailWidth}px`,
          height: `${thumbnailHeight}px`,
        }}
      >
        {/* Default content rendering */}
        {page.content || (
          <div className="text-center p-4">
            <div className="text-sm font-medium text-gray-700">{page.label || `Page ${index + 1}`}</div>
            <div className="text-xs text-gray-400 mt-1 font-mono">{page.id}</div>
          </div>
        )}
      </div>

      {/* Page number badge */}
      <div className="absolute top-2 left-2 z-20">
        <Badge variant="secondary" className={`text-xs min-w-[24px] h-6 font-medium bg-white/95 backdrop-blur-sm flex items-center justify-center shadow-sm border border-gray-200 ${isLocked ? 'opacity-75' : ''}`}>
          {isLocked ? (
            <Lock className="size-3 text-gray-500" />
          ) : (
            <>
              <span className="group-hover/drag-item:hidden">{index + 1}</span>
              <GripVertical className="size-4 text-gray-400 hidden group-hover/drag-item:block" />
            </>
          )}
        </Badge>
      </div>
    </div>
  )
}

export function PageOrderDialog<T extends PageItem>({
  open,
  onOpenChange,
  pages,
  onReorder,
  renderThumbnail,
  pageComponents,
  payload,
  setup,
  title = "Reorder Pages",
  description = "Drag and drop pages to change their order.",
  gridColsClass = "grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-6 gap-6",
}: PageOrderDialogProps<T>) {
  // Track local order changes - only apply on save
  const [localPages, setLocalPages] = React.useState<T[]>(pages)
  const [hasChanges, setHasChanges] = React.useState(false)

  // Sync local state when pages prop changes (but only if dialog is closed or no local changes)
  React.useEffect(() => {
    if (!open) {
      setLocalPages(pages)
      setHasChanges(false)
    } else if (!hasChanges) {
      setLocalPages(pages)
    }
  }, [pages, open, hasChanges])

  const handleLocalReorder = (newOrder: T[]) => {
    setLocalPages(newOrder)
    setHasChanges(true)
  }

  const handleSave = () => {
    onReorder(localPages)
    setHasChanges(false)
    onOpenChange(false)
  }

  const handleCancel = () => {
    setLocalPages(pages)
    setHasChanges(false)
    onOpenChange(false)
  }

  // Auto-create default renderThumbnail if pageComponents provided and no valid custom renderThumbnail
  const defaultRenderThumbnail = React.useMemo(() => {    
    // Only auto-create if renderThumbnail is not provided or is not a function
    if ((!renderThumbnail || typeof renderThumbnail !== 'function') && pageComponents) {
      return createDefaultRenderThumbnail({ pageComponents, payload, setup })
    }
    return null
  }, [renderThumbnail, pageComponents, payload, setup])
  
  const renderPageThumbnail = (page: T, index: number, isDragging?: boolean) => {
    // Priority: 1. Custom renderThumbnail (if it's a function), 2. Auto-created defaultRenderThumbnail, 3. DefaultPageThumbnail
    if (renderThumbnail && typeof renderThumbnail === 'function') {
      return renderThumbnail(page, index, isDragging)
    }
    if (defaultRenderThumbnail) {
      return defaultRenderThumbnail(page as any, index, isDragging)
    }
    return <DefaultPageThumbnail page={page} index={index} isDragging={isDragging} />
  }

  const renderEmptyState = () => (
    <div className="text-center py-20">
      <div className="w-12 h-12 bg-gray-50 rounded-lg flex items-center justify-center mx-auto mb-3">
        <ArrowUpDown className="w-6 h-6 text-gray-400" />
      </div>
      <div className="text-base font-medium text-gray-900 mb-1">No pages found</div>
      <p className="text-sm text-gray-500">Add some pages to get started with reordering.</p>
    </div>
  )

  const keyExtractor = (page: T) => page.id

  // Check if an item has strictPosition and should be disabled from dragging
  const isItemDisabled = React.useCallback((page: T) => {
    const strictPosition = (page as any).strictPosition;
    return strictPosition === 'start' || strictPosition === 'end';
  }, []);

  // Check if an item can be dropped at a specific position
  const canDropAt = React.useCallback((draggedItem: T, targetIndex: number, items: T[]) => {
    const draggedPosition = (draggedItem as any).strictPosition;
    
    // Items with strictPosition cannot be dragged (should be caught by isItemDisabled, but double-check)
    if (draggedPosition === 'start' || draggedPosition === 'end') {
      return false;
    }

    // Find the boundaries of start, middle, and end sections
    let startEndIndex = -1;
    let endStartIndex = items.length;
    
    for (let i = 0; i < items.length; i++) {
      const position = (items[i] as any).strictPosition;
      if (position === 'start') {
        startEndIndex = i;
      } else if (position === 'end' && endStartIndex === items.length) {
        endStartIndex = i;
        // Don't break - continue to find all end items
      }
    }

    // Regular items can only be dropped in the middle section
    // (after all start items and before all end items)
    if (targetIndex <= startEndIndex) {
      return false;
    }

    if (targetIndex >= endStartIndex) {
      return false;
    }

    return true;
  }, []);

  return (
    <Sheet open={open} onOpenChange={(newOpen) => {
      // Only allow closing via explicit cancel/save buttons
      if (!newOpen) {
        handleCancel()
      }
    }}>
      <SheetContent 
        side="bottom" 
        className="h-[90vh] p-0 gap-0 w-full max-w-none rounded-t-md flex flex-col [&>button]:hidden"
        onPointerDownOutside={(e) => {
          // Prevent closing when clicking outside - use cancel/save buttons instead
          e.preventDefault()
        }}
        onEscapeKeyDown={(e) => {
          // Prevent escape from closing - use cancel button instead
          e.preventDefault()
        }}
        data-uhuu-editor
      >
        <SheetHeader className="border-b border-gray-200 p-4">
          <div className="flex items-start justify-between">
            <div className="flex-1">
              <SheetTitle className="text-lg font-medium text-gray-900">{title}</SheetTitle>
              <SheetDescription className="text-sm text-gray-500 mt-1.5">{description}</SheetDescription>
            </div>
            <Badge variant="outline" className="ml-4 text-xs">
              {localPages.length} {localPages.length === 1 ? "page" : "pages"}
            </Badge>
          </div>
        </SheetHeader>

        <div className="flex-1 overflow-hidden flex flex-col">
          <div className="flex-1 overflow-auto px-6 py-6">
            <DragDropGrid
              items={localPages}
              onChange={handleLocalReorder}
              renderItem={renderPageThumbnail}
              keyExtractor={keyExtractor}
              renderEmptyState={renderEmptyState}
              gridColsClass={gridColsClass}
              className="pb-4"
              isItemDisabled={isItemDisabled}
              canDropAt={canDropAt}
            />
          </div>
        </div>

        <SheetFooter className="border-t border-gray-200 px-4 py-3 gap-3">
          <Button
            variant="outline"
            onClick={handleCancel}
          >
            Cancel
          </Button>
          <Button
            variant="default"
            onClick={handleSave}
            disabled={!hasChanges}
          >
            Save Changes
          </Button>
        </SheetFooter>
      </SheetContent>
    </Sheet>
  )
}
