import React, { useEffect } from 'react';
import CompatibilityUtils from './pagination/CompatibilityUtils';
import DocumentUtils from './pagination/DocumentUtils';
import PageSizeUtils from '../utility/PageSizeUtils';
import ConfigProvider from '../config/config-provider';
import './pagination/print.css';
import './pagination/pagination-static.css';
import './../styles/uhuu-indicators.css';

interface PaginationProps {
  children: React.ReactNode;
  className?: string;
  setup: any;
}

export const Pagination: React.FC<PaginationProps> = ({ children, className, setup }) => {
  const config = PageSizeUtils.pageParams("static", setup);

  useEffect(() => {
    if (config?.page?.compatibility) {
      CompatibilityUtils.handle();
    }

    const styleLink = DocumentUtils.setupPageStyles(config?.page?.printCssUrl);

    return () => {
      if (styleLink) {
        DocumentUtils.removePageStyles(styleLink as HTMLLinkElement);
      }
    };
  }, [setup, config?.page?.compatibility, config?.page?.printCssUrl]);

  const combinedClassName = [className, config?.page?.preview].filter(Boolean).join(' ');

  return (
    <ConfigProvider config={config}>
      <div className={combinedClassName}>
        {children}
      </div>
    </ConfigProvider>
  );
};

export default Pagination;
