import * as React from 'react';
import { MoreHorizontal, Trash2 } from 'lucide-react';
import { Button } from '../../ui/button';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
  DropdownMenuSeparator,
} from '../../ui/dropdown-menu';
import { PageOption } from './page-editor';
import { PageItem } from './page-group-utils';
import { PageOptionsRenderer } from './page-options-renderer';

export interface PageOptionsDropdownProps {
  pageOptions: PageOption[];
  targetItem: PageItem | undefined;
  onChange: (pageOption: PageOption, targetItem: PageItem, value: string) => void;
  onRemove?: (targetId: string) => void;
  showRemove?: boolean;
  title?: string;
  removeLabel?: string;
  triggerClassName?: string;
}

export function PageOptionsDropdown({
  pageOptions,
  targetItem,
  onChange,
  onRemove,
  showRemove = false,
  title = 'Options',
  removeLabel = 'Remove',
  triggerClassName,
}: PageOptionsDropdownProps) {
  if (!targetItem) return null;

  return (
    <DropdownMenu modal={false}>
      <DropdownMenuTrigger asChild className={triggerClassName || 'page-options-trigger'}>
        <Button
          variant="ghost"
          size="sm"
          className="h-7 w-7"
          title={title}
        >
          <MoreHorizontal className="w-4 h-4" />
          <span className="sr-only">{title}</span>
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent className="min-w-48 p-3" align="center">
        <PageOptionsRenderer
          pageOptions={pageOptions}
          targetItem={targetItem}
          onChange={onChange}
        />
        {showRemove && onRemove && (
          <>
            {pageOptions.length > 0 && <DropdownMenuSeparator className="my-2" />}
            <DropdownMenuItem
              onClick={() => onRemove(targetItem.id)}
              className="text-red-600 focus:text-red-700 focus:bg-red-50"
            >
              <Trash2 className="w-4 h-4 mr-2" />
              <span>{removeLabel}</span>
            </DropdownMenuItem>
          </>
        )}
      </DropdownMenuContent>
    </DropdownMenu>
  );
}

