import * as React from 'react';
import type { PageDataBinding } from './data-binding';
import { getDialogProps } from '../../editable/dialog-props';

/**
 * Dialog configuration options
 */
export type DialogOptions = {
  type?: 'image' | 'text' | 'assistant' | 'markdown' | 'spreadsheet' | 'map';
  imagePath?: string;
  imageGalleryPath?: string;
  ratio?: number;
  rows?: number;
  value?: any;
  payload?: any;
  [key: string]: any;
};

/**
 * Dialog configuration returned by dialog builders
 */
export type DialogConfig = {
  path: string;
  subPath?: string | null;
  imageGalleryPath?: string;
  type?: string;
  imagePath?: string;
  ratio?: number;
  rows?: number;
  value?: any;
  payload?: any;
  [key: string]: any;
};

/**
 * Config-based resolver: map of keys to path strings or functions
 * Path strings support optional chaining (e.g., 'integration.listing ?? integration.pba_listing')
 */
export type IntegrationResolverConfig = {
  [key: string]: string | ((integration: any) => any);
};

/**
 * Function-based resolver: complex extraction logic
 */
export type IntegrationResolverFunction = (
  integration: any,
  payload?: any
) => any;

/**
 * Union type for resolver
 */
export type IntegrationResolver = IntegrationResolverConfig | IntegrationResolverFunction;

/**
 * Gallery path resolver: string path or function
 */
export type GalleryPathResolver = string | ((integration: any) => string | null);

/**
 * Integration adapter configuration
 */
export type IntegrationAdapterConfig = {
  dataBinding: PageDataBinding;
  integration: any;
  resolver: IntegrationResolver;
  galleryPath?: GalleryPathResolver;
  defaults?: Record<string, any>;
};

/**
 * Integration adapter API returned by useIntegrationAdapter
 */
export type IntegrationAdapter = {
  // Resolved data (e.g., { listing, property, media })
  data: any;
  
  // Dialog builder that automatically uses dataBinding
  dialog: (subPath: string, options?: DialogOptions, value?: any) => DialogConfig | null;
  
  // Dialog props helper (combines dialog + getDialogProps)
  dialogProps: (subPath: string, options?: DialogOptions, value?: any) => Record<string, any>;
  
  // Gallery path (resolved from config or integration structure)
  galleryPath: string | null;
  
  // Integration instance info
  instanceId: string | null;
  integration: any;
};

/**
 * Evaluate a path string with optional chaining support
 * Supports patterns like: 'integration.listing ?? integration.pba_listing'
 */
function evaluatePathString(path: string, integration: any): any {
  if (!path || !integration) return undefined;
  
  // Handle nullish coalescing operator (??)
  if (path.includes('??')) {
    const parts = path.split('??').map(p => p.trim());
    for (const part of parts) {
      const value = getNestedValue(integration, part);
      if (value !== undefined && value !== null) {
        return value;
      }
    }
    return undefined;
  }
  
  return getNestedValue(integration, path);
}

/**
 * Get nested value from object using dot notation
 */
function getNestedValue(obj: any, path: string): any {
  if (!path) return obj;
  const keys = path.split('.');
  let current = obj;
  for (const key of keys) {
    if (current == null) return undefined;
    current = current[key];
  }
  return current;
}

/**
 * Resolve integration data using config-based resolver
 */
function resolveWithConfig(
  config: IntegrationResolverConfig,
  integration: any,
  payload?: any
): any {
  const result: any = {};
  
  for (const [key, resolver] of Object.entries(config)) {
    if (typeof resolver === 'function') {
      result[key] = resolver(integration);
    } else if (typeof resolver === 'string') {
      // Remove 'integration.' prefix if present (it's implicit)
      const path = resolver.startsWith('integration.') 
        ? resolver.slice('integration.'.length)
        : resolver;
      result[key] = evaluatePathString(path, integration);
    }
  }
  
  return result;
}

/**
 * Resolve integration data using function-based resolver
 */
function resolveWithFunction(
  resolver: IntegrationResolverFunction,
  integration: any,
  payload?: any
): any {
  return resolver(integration, payload);
}

/**
 * Resolve integration data using the configured resolver
 */
function resolveIntegrationData(
  resolver: IntegrationResolver,
  integration: any,
  payload?: any
): any {
  if (typeof resolver === 'function') {
    return resolveWithFunction(resolver, integration, payload);
  } else {
    return resolveWithConfig(resolver, integration, payload);
  }
}

/**
 * Resolve gallery path from various sources
 */
function resolveGalleryPath(
  dataBinding: PageDataBinding,
  integration: any,
  configGalleryPath?: GalleryPathResolver
): string | null {
  // Priority 1: dataBinding.defaults.imageGalleryPath (from integration meta/config)
  if (dataBinding?.defaults?.imageGalleryPath) {
    return dataBinding.defaults.imageGalleryPath;
  }
  
  // Priority 2: Config-provided gallery path resolver
  if (configGalleryPath) {
    if (typeof configGalleryPath === 'function') {
      const resolved = configGalleryPath(integration);
      if (resolved) return resolved;
    } else if (typeof configGalleryPath === 'string') {
      return configGalleryPath;
    }
  }
  
  // Priority 3: Try to infer from integration structure
  if (integration?.media?.images) return 'media.images';
  if (integration?.listing?.media?.images) return 'listing.media.images';
  if (integration?.pba_listing?.media?.images) return 'pba_listing.media.images';
  if (integration?.property?.media?.images) return 'property.media.images';
  
  return null;
}

/**
 * Build integration dialog path using dataBinding
 */
function buildDialogPath(
  dataBinding: PageDataBinding,
  subPath: string,
  isAssistant: boolean
): string | null {
  if (!dataBinding?.integration?.path) return null;
  
  if (isAssistant) {
    // For assistant dialogs, include subPath in the main path
    return dataBinding.integration.path(subPath);
  } else {
    // For other dialogs, get base path and subPath is handled separately
    return dataBinding.integration.path();
  }
}

/**
 * Build dialog configuration
 */
function buildDialogConfig(
  dataBinding: PageDataBinding,
  subPath: string,
  options: DialogOptions = {},
  value?: any,
  galleryPath: string | null = null
): DialogConfig | null {
  const integrationPath = dataBinding?.integration?.path?.();
  if (!integrationPath) return null;
  
  const isAssistant = options.type === 'assistant';
  const isImage = options.type === 'image' || options.imagePath;
  
  // Build the full path
  const fullPath = isAssistant
    ? dataBinding.integration.path(subPath) ?? [integrationPath, subPath].filter(Boolean).join('.')
    : [integrationPath, subPath].filter(Boolean).join('.');
  
  // For image dialogs, resolve gallery path
  if (isImage) {
    const resolvedGalleryPath = options.imageGalleryPath 
      ?? (galleryPath ? `${integrationPath}.${galleryPath}` : null)
      ?? dataBinding.defaults.imageGalleryPath;
    
    return {
      path: fullPath,
      imagePath: options.imagePath || 'url',
      imageGalleryPath: resolvedGalleryPath,
      type: options.type || 'image',
      ratio: options.ratio,
      value,
      payload: options.payload ?? dataBinding.payload,
      ...options,
    };
  }
  
  // For assistant dialogs, path includes subPath
  if (isAssistant) {
    return {
      path: fullPath,
      type: 'assistant',
      rows: options.rows,
      value,
      payload: options.payload ?? dataBinding.payload,
      ...options,
    };
  }
  
  // For other dialogs
  return {
    path: integrationPath,
    subPath,
    type: options.type || 'text',
    rows: options.rows,
    value,
    payload: options.payload ?? dataBinding.payload,
    ...options,
  };
}

/**
 * Hook to create an integration adapter for easy data resolution and dialog building
 * 
 * @example
 * ```tsx
 * const adapter = useIntegrationAdapter({
 *   dataBinding,
 *   integration,
 *   resolver: {
 *     listing: 'listing ?? pba_listing',
 *     property: 'property',
 *     media: 'media',
 *   },
 *   galleryPath: (integration) => {
 *     if (integration?.media?.images) return 'media.images';
 *     return 'listing.media.images';
 *   },
 * });
 * 
 * // Use resolved data
 * const { listing, property, media } = adapter.data;
 * 
 * // Use dialog props
 * <div {...adapter.dialogProps('overrides.heroImage', { type: 'image', imagePath: 'url' }, heroImage)}>
 * ```
 */
export function useIntegrationAdapter(
  config: IntegrationAdapterConfig
): IntegrationAdapter {
  const { dataBinding, integration, resolver, galleryPath: configGalleryPath, defaults } = config;
  
  // Resolve integration data
  const resolvedData = React.useMemo(() => {
    return resolveIntegrationData(resolver, integration, dataBinding?.payload);
  }, [resolver, integration, dataBinding?.payload]);
  
  // Resolve gallery path
  const galleryPath = React.useMemo(() => {
    return resolveGalleryPath(dataBinding, integration, configGalleryPath);
  }, [dataBinding, integration, configGalleryPath]);
  
  // Dialog builder
  const dialog = React.useCallback(
    (subPath: string, options: DialogOptions = {}, value?: any): DialogConfig | null => {
      return buildDialogConfig(
        dataBinding,
        subPath,
        options,
        value,
        galleryPath
      );
    },
    [dataBinding, galleryPath]
  );
  
  // Dialog props builder (combines dialog + getDialogProps)
  const dialogProps = React.useCallback(
    (subPath: string, options: DialogOptions = {}, value?: any): Record<string, any> => {
      const dialogConfig = dialog(subPath, options, value);
      if (!dialogConfig) return {};
      
      // Use the getDialogProps utility from editable
      // getDialogProps expects { dialog } and optional config
      const props = getDialogProps({ dialog: dialogConfig }, { page: { paginationType: 'static' } });
      
      // Ensure stopPropagation is called on click
      if (props.onClick) {
        const originalOnClick = props.onClick;
        props.onClick = (e: any) => {
          e.stopPropagation();
          originalOnClick(e);
        };
      }
      
      return props;
    },
    [dialog]
  );
  
  return React.useMemo(
    () => ({
      data: resolvedData,
      dialog,
      dialogProps,
      galleryPath,
      instanceId: dataBinding?.integration?.instanceId ?? null,
      integration,
    }),
    [resolvedData, dialog, dialogProps, galleryPath, dataBinding, integration]
  );
}

