import * as React from 'react';
import type { PageOption } from './page-editor';
import { mergePageEditorStateIntoPayload, PageItem, DEFAULT_PAGE_EDITOR_STATE_KEY } from './page-group-utils';
import { updateIntegrationByPath } from './integration-utils';

type PagePayloadTarget = string | { id?: string; templateId?: string; componentKey?: string } | PageItem | null | undefined;

export function getPagePayload(payload: any, target: PagePayloadTarget) {
  if (!payload || !target) return undefined;
  
  const targetId = typeof target === 'string' ? target : target?.id;
  const templateId = typeof target === 'string' ? undefined : (target as any)?.templateId ?? (target as any)?.componentKey;
  const fallbackId = typeof target === 'string' ? undefined : (target as any)?.componentKey;
  const candidateKeys = Array.from(
    new Set(
      [targetId, templateId, fallbackId].filter(Boolean) as string[]
    )
  );

  for (const key of candidateKeys) {
    if (payload?.pages?.[key] !== undefined) return payload.pages[key];
  }

  // Allow group-level payloads
  for (const key of candidateKeys) {
    if (payload?.groups?.[key] !== undefined) return payload.groups[key];
  }

  // Legacy fallback: top-level keys keyed by id/templateId
  for (const key of candidateKeys) {
    if ((payload as any)[key] !== undefined) return (payload as any)[key];
  }

  return undefined;
}

type TemplateDataContextValue = {
  payload: any;
  setPayload: (next: any | ((prev: any) => any)) => void;
  setPageOptionValue: (pageOrGroupId: string, key: string, value: any) => void;
  setIntegrationPayload: (instanceId: string, next: any | ((prev: any) => any)) => void;
  setIntegrationPayloadValue: (instanceId: string, key: string, value: any) => void;
  removeIntegrationPayload: (instanceId: string) => void;
  updateIntegrationByDialogPath: (path: string, value: any) => void;
  mergePageEditorState: (items: PageItem[], key?: string) => void;
  getPagePayload: (target: PagePayloadTarget) => any;
};

export const TemplateDataContext = React.createContext<TemplateDataContextValue | null>(null);

/**
 * Get the list of reserved keys that the page editor manages.
 * These keys are isolated from external payload updates.
 */
function getReservedKeys(stateKey: string = DEFAULT_PAGE_EDITOR_STATE_KEY): string[] {
  return [stateKey];
}

/**
 * Merge external payload while preserving reserved keys managed by page editor.
 * External payload is the source of truth for all keys except reserved ones.
 */
function mergeExternalPayload(
  currentPayload: any,
  externalPayload: any,
  reservedKeys: string[]
): any {
  if (!externalPayload) return currentPayload;
  if (!currentPayload) return externalPayload;

  // Start with external payload (source of truth for non-reserved keys)
  const merged = { ...externalPayload };
  
  // Overlay reserved keys from current payload (page editor owns these)
  reservedKeys.forEach(key => {
    if (currentPayload[key] !== undefined) {
      merged[key] = currentPayload[key];
    }
  });

  return merged;
}

export function TemplateDataProvider({
  payload,
  onPayloadChange,
  children,
  stateKey = DEFAULT_PAGE_EDITOR_STATE_KEY,
}: {
  payload?: any;
  onPayloadChange?: (nextPayload: any) => void;
  children: React.ReactNode;
  stateKey?: string;
}) {
  const [currentPayload, setCurrentPayload] = React.useState(payload ?? {});
  const lastExternalPayloadRef = React.useRef<any>(null);
  const isInternalUpdateRef = React.useRef(false);
  const lastInternalPayloadRef = React.useRef<any>(null);
  const lastInternalTimestampRef = React.useRef<number>(0);
  const isInitialMountRef = React.useRef(true);

  // Serialize payload for comparison (simple deep equality check)
  const serializePayload = React.useCallback((p: any) => {
    try {
      return JSON.stringify(p);
    } catch {
      return String(p);
    }
  }, []);

  // Get reserved keys for this provider instance
  const reservedKeys = React.useMemo(() => getReservedKeys(stateKey), [stateKey]);

  // Helper to create payload without reserved keys for comparison
  const payloadWithoutReservedKeys = React.useCallback((p: any, keys: string[]) => {
    if (!p) return null;
    const filtered = { ...p };
    keys.forEach(key => {
      delete filtered[key];
    });
    return filtered;
  }, []);

  // When external payload changes, merge it while preserving reserved keys
  React.useEffect(() => {
    // Handle initial mount - set initial payload
    if (isInitialMountRef.current) {
      isInitialMountRef.current = false;
      if (payload) {
        console.log('[TemplateDataProvider] Initial mount - setting payload:', payload);
        lastExternalPayloadRef.current = payload;
        setCurrentPayload(payload);
      }
      return;
    }

    // Skip if this is an internal update (from setPayload)
    if (isInternalUpdateRef.current) {
      isInternalUpdateRef.current = false;
      console.log('[TemplateDataProvider] Skipping internal update');
      // Store the payload we just set internally to detect round-trips
      lastInternalPayloadRef.current = payload;
      lastInternalTimestampRef.current = Date.now();
      // Update lastExternalPayloadRef to prevent immediate re-merge
      lastExternalPayloadRef.current = payload;
      return;
    }

    // Skip if payload hasn't actually changed (same object reference)
    if (payload === lastExternalPayloadRef.current) {
      console.log('[TemplateDataProvider] Skipping - same object reference');
      return;
    }

    // Check if this is a round-trip by comparing non-reserved keys only
    // Only check for round-trips within a short time window (500ms) to avoid blocking legitimate updates
    const timeSinceLastInternal = Date.now() - lastInternalTimestampRef.current;
    const isRecentInternalUpdate = timeSinceLastInternal < 500;
    
    if (isRecentInternalUpdate && lastInternalPayloadRef.current !== null) {
      const currentWithoutReserved = payloadWithoutReservedKeys(payload, reservedKeys);
      const lastInternalWithoutReserved = payloadWithoutReservedKeys(lastInternalPayloadRef.current, reservedKeys);
      
      const currentSerialized = currentWithoutReserved ? serializePayload(currentWithoutReserved) : null;
      const lastInternalSerialized = lastInternalWithoutReserved ? serializePayload(lastInternalWithoutReserved) : null;
      
      if (currentSerialized && currentSerialized === lastInternalSerialized) {
        // This is likely a round-trip - skip it but update the ref
        console.log('[TemplateDataProvider] Skipping round-trip');
        lastInternalPayloadRef.current = null; // Clear after one check
        lastExternalPayloadRef.current = payload;
        return;
      }
    }

    // This is a genuine external update from $uhuu.listen or other external source - merge it
    console.log('[TemplateDataProvider] External payload update detected, merging...', payload);
    lastExternalPayloadRef.current = payload;
    setCurrentPayload((prev) => {
      if (!payload) return prev;
      const merged = mergeExternalPayload(prev, payload, reservedKeys);
      console.log('[TemplateDataProvider] Merged payload:', merged);
      return merged;
    });
  }, [payload, reservedKeys, serializePayload, payloadWithoutReservedKeys]);

  const notifyHost = React.useCallback(
    (next: any) => {
      onPayloadChange?.(next);
      if (typeof window !== 'undefined' && (window as any).$uhuu?.initPayload) {
        (window as any).$uhuu.initPayload(next);
      }
    },
    [onPayloadChange]
  );

  /**
   * Handle payload updates, including integration path updates from dialogs
   * When external payload updates come in with integration paths, route them correctly
   */
  const setPayload = React.useCallback(
    (next: any | ((prev: any) => any)) => {
      isInternalUpdateRef.current = true;
      setCurrentPayload((prev) => {
        const resolved = typeof next === 'function' ? (next as (prev: any) => any)(prev) : next;
        
        // Check if this update contains integration path updates from dialogs
        // The SDK/document editor may send updates with paths like "integrations.id.field"
        // We need to ensure these are properly structured
        let finalPayload = resolved;
        
        // If the payload has a _dialogUpdate flag or similar, process integration paths
        // This handles cases where dialogs update integration data via path-based updates
        if (resolved && typeof resolved === 'object') {
          // Check for any top-level keys that might be integration paths (from dialog updates)
          // The document editor might send updates that need to be routed to integrations
          const integrationKeys = Object.keys(resolved).filter(key => 
            key.startsWith('integrations.') || key === 'integrations'
          );
          
          if (integrationKeys.length > 0 && resolved.integrations) {
            // Integration data is already properly structured, use as-is
            finalPayload = resolved;
          }
        }
        
        // Store the resolved payload to detect round-trips
        lastInternalPayloadRef.current = finalPayload;
        lastInternalTimestampRef.current = Date.now();
        notifyHost(finalPayload);
        return finalPayload;
      });
    },
    [notifyHost]
  );

  const setPageOptionValue = React.useCallback(
    (pageOrGroupId: string, key: string, value: any) => {
      setPayload((prev: any) => ({
        ...(prev ?? {}),
        pages: {
          ...(prev?.pages ?? {}),
          [pageOrGroupId]: {
            ...(prev?.pages?.[pageOrGroupId] ?? {}),
            [key]: value,
          },
        },
      }));
    },
    [setPayload]
  );

  const setIntegrationPayload = React.useCallback(
    (instanceId: string, next: any | ((prev: any) => any)) => {
      setPayload((prev: any) => {
        const prevIntegrations = prev?.integrations ?? {};
        const current = prevIntegrations[instanceId];
        const resolved = typeof next === 'function' ? (next as (prev: any) => any)(current) : next;
        return {
          ...(prev ?? {}),
          integrations: {
            ...prevIntegrations,
            [instanceId]: resolved,
          },
        };
      });
    },
    [setPayload]
  );

  const setIntegrationPayloadValue = React.useCallback(
    (instanceId: string, key: string, value: any) => {
      setIntegrationPayload(instanceId, (prevIntegration: any) => ({
        ...(prevIntegration ?? {}),
        [key]: value,
      }));
    },
    [setIntegrationPayload]
  );

  const removeIntegrationPayload = React.useCallback(
    (instanceId: string) => {
      setPayload((prev: any) => {
        if (!prev?.integrations || !prev.integrations[instanceId]) {
          return prev;
        }
        const { [instanceId]: removed, ...remainingIntegrations } = prev.integrations;
        return {
          ...prev,
          integrations: Object.keys(remainingIntegrations).length > 0 ? remainingIntegrations : undefined,
        };
      });
    },
    [setPayload]
  );

  /**
   * Update integration data using a path-based update (for dialog compatibility)
   * Handles paths like "integrations.instanceId.field.subfield"
   */
  const updateIntegrationByDialogPath = React.useCallback(
    (path: string, value: any) => {
      setPayload((prev: any) => updateIntegrationByPath(prev, path, value));
    },
    [setPayload]
  );

  const mergePageEditorState = React.useCallback(
    (items: PageItem[], key?: string) => {
      const effectiveKey = key ?? stateKey;
      setPayload((prev: any) => mergePageEditorStateIntoPayload(prev, items, effectiveKey));
    },
    [setPayload, stateKey]
  );

  const resolvePagePayload = React.useCallback(
    (target: PagePayloadTarget) => getPagePayload(currentPayload, target),
    [currentPayload]
  );

  const value = React.useMemo(
    () => ({
      payload: currentPayload,
      setPayload,
      setPageOptionValue,
      setIntegrationPayload,
      setIntegrationPayloadValue,
      removeIntegrationPayload,
      updateIntegrationByDialogPath,
      mergePageEditorState,
      getPagePayload: resolvePagePayload,
    }),
    [
      currentPayload,
      setPayload,
      setPageOptionValue,
      setIntegrationPayload,
      setIntegrationPayloadValue,
      removeIntegrationPayload,
      updateIntegrationByDialogPath,
      mergePageEditorState,
      resolvePagePayload,
    ]
  );

  return <TemplateDataContext.Provider value={value}>{children}</TemplateDataContext.Provider>;
}

export function useTemplateData() {
  const ctx = React.useContext(TemplateDataContext);
  if (!ctx) {
    throw new Error('useTemplateData must be used within a TemplateDataProvider');
  }
  return ctx;
}

// JSON-friendly option builder: definitions  PageOption[] bound to payload.pages
export type PayloadOptionDefinition = Omit<PageOption, 'getValue' | 'onChange' | 'applyPatch'> & {
  defaultValue?: any;
  field?: string;
};

function toDefault(def: PayloadOptionDefinition) {
  if (def.defaultValue !== undefined) return def.defaultValue;
  if (def.type === 'toggle') return false;
  if (def.type === 'slider' || def.type === 'counter') return 0;
  return '';
}

function parseValue(def: PayloadOptionDefinition, raw: string | number | boolean) {
  if (def.type === 'toggle') return raw === true || raw === 'true';
  if (def.type === 'slider' || def.type === 'counter') return Number(raw);
  return raw;
}

export function buildPayloadPageOption(
  def: PayloadOptionDefinition,
  payload: any,
  setPageOptionValue: (pageOrGroupId: string, key: string, value: any) => void
): PageOption {
  const field = def.field ?? def.id;
  const base = def as Omit<PageOption, 'getValue' | 'onChange' | 'applyPatch'>;
  return {
    ...base,
    getValue: (item: PageItem) => {
      const stored = payload?.pages?.[item.id]?.[field];
      if (stored === undefined) return toDefault(def);
      if (def.type === 'toggle') return !!stored;
      return stored;
    },
    onChange: (pageOrGroupId: string, value: string) => {
      setPageOptionValue(pageOrGroupId, field, parseValue(def, value));
    },
  } as PageOption;
}

export function buildPayloadPageOptions(
  definitions: PayloadOptionDefinition[],
  payload: any,
  setPageOptionValue: (pageOrGroupId: string, key: string, value: any) => void
): PageOption[] {
  return definitions.map((def) =>
    buildPayloadPageOption(def, payload, setPageOptionValue)
  );
}

export function usePayloadPageOptions(definitions: PayloadOptionDefinition[]): PageOption[] {
  const { payload, setPageOptionValue } = useTemplateData();

  return React.useMemo(
    () => buildPayloadPageOptions(definitions, payload, setPageOptionValue),
    [definitions, payload, setPageOptionValue]
  );
}
