import * as React from 'react';
import { Section } from '../resizers/section-page-resizer';
import { PageGroup as PageGroupType, StandalonePage, PageItem } from './page-group-utils';
import { Badge } from '../../ui/badge';
import { PageOption } from './page-editor';
import { PageOptionsDropdown } from './page-options-dropdown';

interface PageGroupProps {
  group: PageGroupType;
  pagesWithPageNum: Array<StandalonePage & { pageNum: number }>;
  onRemove: (groupId: string) => void;
  totalItems: number;
  renderPage: (page: StandalonePage & { pageNum: number }) => React.ReactNode;
  pageOptions: PageOption[];
  onPageOptionChange: (pageOption: PageOption, target: PageItem, value: string) => void;
  onLabelChange?: (groupId: string, label: string) => void;
  // MVP: Minimal props, no advanced customization
}

export function PageGroup({
  group,
  pagesWithPageNum,
  onRemove,
  totalItems,
  renderPage,
  pageOptions,
  onPageOptionChange,
  onLabelChange,
}: PageGroupProps) {
  const handleLabelClick = () => {
    if (!onLabelChange) return;
    const currentLabel = group.label || group.id;
    const entered = window.prompt('Rename group:', currentLabel);
    if (entered !== null) {
      const trimmed = entered.trim();
      onLabelChange(group.id, trimmed || '');
    }
  };
  return (
    <>
      {pagesWithPageNum.map((page, indexInGroup) => {
        const isFirstPage = indexInGroup === 0;
        
        return (
          <Section
            key={page.id}
            title={`Sheet ${page.pageNum}`}
            controls={
              isFirstPage ? (
                // Group-level controls only on first page
                <div data-uhuu-editor className="pl-0 py-1.5 flex justify-between items-center">
                  <div className="flex items-center gap-2">
                    <span className="page-number">
                      {page.pageNum}
                    </span>
                    {page.label && (
                      <span className="text-xs text-gray-500">
                        {page.label}
                      </span>
                    )}
                    <Badge 
                      variant="outline" 
                      className="text-xs font-normal text-gray-600 border-gray-200 bg-transparent cursor-pointer hover:bg-gray-50 transition-colors"
                      onClick={handleLabelClick}
                      title="Click to rename"
                      data-group-id={group.id}
                    >
                      {group.label || group.id}
                    </Badge>
                  </div>
                  <div className="flex items-center">
                    <PageOptionsDropdown
                      pageOptions={pageOptions}
                      targetItem={group}
                      onChange={onPageOptionChange}
                      onRemove={onRemove}
                      showRemove={totalItems > 1}
                      title="Group options"
                      removeLabel="Remove Group"
                    />
                  </div>
                </div>
              ) : (
                // No controls for pages within group (group internal pages are not directly editable)
                <div className="pl-0 pr-3 py-1.5 flex justify-between items-center">
                  <div className="flex items-center gap-2">
                    <span className="page-number">
                      {page.pageNum}
                    </span>
                    {page.label && (
                      <span className="text-xs text-gray-500">
                        {page.label}
                      </span>
                    )}
                    <span className="text-xs text-gray-400"></span>
                  </div>
                </div>
              )
            }
          >
            {renderPage(page)}
          </Section>
        );
      })}
    </>
  );
}
