import * as React from "react"
import { useState, useRef, useEffect, createContext, useContext } from "react"
import { Button } from "../../ui/button"
import { ZoomIn, ZoomOut, RotateCcw, Plus, ChevronDown, UnfoldVertical, UnfoldHorizontal, Maximize } from "lucide-react"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
  DropdownMenuSeparator,
} from "../../ui/dropdown-menu"
import { useShouldHideUI } from "../interactive-mode-context"
import "./section-page-resizer.css"

// Context for sharing zoom state across all sections
interface ZoomContextType {
  zoom: number
  scaleValue: number
  hideUI: boolean
}

const ZoomContext = createContext<ZoomContextType>({ zoom: 100, scaleValue: 1, hideUI: false })

interface SectionPageResizerProps {
  children: React.ReactNode
  className?: string
  defaultZoom?: number
  minZoom?: number
  maxZoom?: number
  onAddPage?: () => void
  menuItems?: React.ReactNode
  hideUI?: boolean
  preview?: PreviewMode
}

export type PreviewMode = "single_page" | "two_pages"

interface TwoPageSectionProps {
  children: React.ReactNode
  /** "spread" = two pages side by side, "left" = single page on left, "right" = single page on right */
  layout?: "spread" | "left" | "right"
}

export function TwoPageSection({ children, layout = "spread" }: TwoPageSectionProps) {
  const { scaleValue } = useContext(ZoomContext)
  const pairRef = useRef<HTMLDivElement>(null)

  // Update pair width based on scaled content dimensions
  useEffect(() => {
    if (!pairRef.current) return

    const updatePairWidth = () => {
      const sections = pairRef.current?.querySelectorAll('[data-section-content]')
      if (!sections?.length) return

      // Sum natural widths of all pages in this pair
      const totalNaturalWidth = Array.from(sections).reduce((sum, section) => {
        const width = Number.parseInt(section.getAttribute('data-natural-width') || '0')
        return sum + width
      }, 0)

      if (totalNaturalWidth > 0) {
        const scaledWidth = totalNaturalWidth * scaleValue
        pairRef.current?.style.setProperty('--uhuu-group-pair-width', `${scaledWidth}px`)
      }
    }

    updatePairWidth()

    const resizeObserver = new ResizeObserver(updatePairWidth)
    const sections = pairRef.current.querySelectorAll('[data-section-content]')
    sections.forEach((section) => resizeObserver.observe(section))

    return () => resizeObserver.disconnect()
  }, [children, scaleValue])

  return (
    <div
      ref={pairRef}
      className={`two-pages-pair two-pages-pair--${layout}`}
    >
      {children}
    </div>
  )
}

interface SectionProps {
  children: React.ReactNode
  title: string
  className?: string
  controls?: React.ReactNode
  origin?: "left" | "right" | "center"
}

type FitMode = "none" | "width" | "height" | "both"

// Individual Section Component
export function Section({ children, title, className = "", controls, origin = "center" }: SectionProps) {
  const { scaleValue, hideUI } = useContext(ZoomContext)
  const contentRef = useRef<HTMLDivElement>(null)
  const [contentHeight, setContentHeight] = useState<number>(0)
  const [contentWidth, setContentWidth] = useState<number>(0)

  // Calculate and update content dimensions
  useEffect(() => {
    if (contentRef.current) {
      const updateDimensions = () => {
        const element = contentRef.current
        if (element) {
          const originalTransform = element.style.transform
          element.style.transform = "scale(1)"

          const naturalHeight = element.scrollHeight
          const naturalWidth = element.scrollWidth

          element.style.transform = originalTransform

          setContentHeight(naturalHeight)
          setContentWidth(naturalWidth)
        }
      }

      updateDimensions()

      const resizeObserver = new ResizeObserver(updateDimensions)
      resizeObserver.observe(contentRef.current)

      return () => {
        resizeObserver.disconnect()
      }
    }
  }, [children])

  const scaledHeight = contentHeight * scaleValue
  const scaledWidth = Math.max(contentWidth * scaleValue, 150)
  
  // Align content based on transform origin (left/right for spreads, center for single pages)
  const alignmentMap = {
    left: { justify: "justify-start", origin: "top left" },
    right: { justify: "justify-end", origin: "top right" },
    center: { justify: "justify-center", origin: "top center" },
  }
  const { justify: contentAlignClass, origin: transformOrigin } = alignmentMap[origin]

  // If hiding UI, render children directly without controls or scaling
  if (hideUI) {
    return <div className={className}>{children}</div>
  }

  return (
    <div 
      className={`group/section ${className}`}
      style={{
        width: `${scaledWidth}px`,
        minWidth: "150px",
      }}
    >
      {/* Section Header */}
      <div>
        {controls ?? (
          <div className="px-4 py-2 border-b border-gray-200">
            <div className="text-sm font-medium text-gray-700">{title} Controls</div>
          </div>
        )}
      </div>

      {/* Section Content - Scaled with dynamic height */}
      <div
        className="pt-1"
        style={{
          height: scaledHeight > 0 ? `${scaledHeight + 32}px` : "auto",
          minHeight: "100px",
        }}
      >
        <div className={`flex items-start ${contentAlignClass}`}>
          <div
            ref={contentRef}
            data-section-content
            data-natural-width={contentWidth}
            data-natural-height={contentHeight}
            style={{
              transform: `scale(${scaleValue})`,
              transformOrigin,
            }}
          >
            {children}
          </div>
        </div>
      </div>
    </div>
  )
}

// Main Container with Controls
export default function SectionPageResizer({
  children,
  className = "",
  defaultZoom = 100,
  minZoom = 25,
  maxZoom = 200,
  onAddPage,
  menuItems,
  hideUI: hideUIProp,
  preview = "single_page",
}: SectionPageResizerProps) {
  const shouldHideUI = useShouldHideUI()
  const hideUI = hideUIProp ?? shouldHideUI
  
  const [zoom, setZoom] = useState(defaultZoom)
  const [fitMode, setFitMode] = useState<FitMode>("none")
  const containerRef = useRef<HTMLDivElement>(null)
  const contentRef = useRef<HTMLDivElement>(null)
  const [autoFitInitialized, setAutoFitInitialized] = useState(false)
  const [lastContainerWidth, setLastContainerWidth] = useState<number>(0)
  const [isInitializing, setIsInitializing] = useState(true)

  // Calculate fit-to-container zoom levels
  const calculateFitZoom = (mode: FitMode) => {
    if (!containerRef.current || !contentRef.current) return zoom

    const container = containerRef.current.getBoundingClientRect()

    const firstSection = contentRef.current.querySelector("[data-section-content]")
    if (!firstSection) return zoom

    let contentWidth = Number.parseInt(firstSection.getAttribute("data-natural-width") || "0")
    const contentHeight = Number.parseInt(firstSection.getAttribute("data-natural-height") || "0")

    if (contentWidth === 0 || contentHeight === 0) return zoom

    // For two-page preview, account for 2x page width when calculating fit to width
    if (preview === "two_pages") {
      contentWidth = contentWidth * 2
    }

    const availableWidth = container.width - 32
    // Use screen height for better "fit to height" calculation, accounting for bottom controls
    const availableHeight = screen.height - 120

    const scaleX = (availableWidth / contentWidth) * 100
    const scaleY = (availableHeight / contentHeight) * 100

    switch (mode) {
      case "width":
        return Math.min(Math.max(scaleX, minZoom), maxZoom)
      case "height":
        return Math.min(Math.max(scaleY, minZoom), maxZoom)
      case "both":
        return Math.min(Math.max(Math.min(scaleX, scaleY), minZoom), maxZoom)
      default:
        return zoom
    }
  }

  // Handle fit mode changes
  const handleFitMode = (mode: FitMode) => {
    setFitMode(mode)
    if (mode !== "none") {
      const newZoom = calculateFitZoom(mode)
      setZoom(newZoom)
    }
  }

  // Zoom in/out buttons with 25% steps
  const zoomIn = () => {
    const newZoom = Math.min(zoom + 25, maxZoom)
    setZoom(newZoom)
    setFitMode("none")
  }

  const zoomOut = () => {
    const newZoom = Math.max(zoom - 25, minZoom)
    setZoom(newZoom)
    setFitMode("none")
  }

  // Reset zoom
  const resetZoom = () => {
    setZoom(defaultZoom)
    setFitMode("none")
  }

  // Handle initialization and window resize
  useEffect(() => {
    const handleInitialization = () => {
      if (!containerRef.current || !contentRef.current) return

      const container = containerRef.current.getBoundingClientRect()
      const currentContainerWidth = container.width

      if (!autoFitInitialized) {
        const firstSection = contentRef.current.querySelector("[data-section-content]")
        if (firstSection) {
          const sectionWidth = Number.parseInt(firstSection.getAttribute("data-natural-width") || "0")
          if (sectionWidth > 0) {
            if (sectionWidth > currentContainerWidth - 48) {
              const newZoom = calculateFitZoom("both")
              setFitMode("both")
              setZoom(newZoom)
            }
            setAutoFitInitialized(true)
            setLastContainerWidth(currentContainerWidth)
            setIsInitializing(false)
            return
          }
        }
        if (isInitializing) {
          setTimeout(handleInitialization, 50)
          return
        }
      }

      setIsInitializing(false)
    }

    const handleResize = () => {
      if (!containerRef.current || isInitializing) return

      const container = containerRef.current.getBoundingClientRect()
      const currentContainerWidth = container.width

      if (Math.abs(currentContainerWidth - lastContainerWidth) > 10) {
        if (fitMode !== "none") {
          const newZoom = calculateFitZoom(fitMode)
          setZoom(newZoom)
        }
        setLastContainerWidth(currentContainerWidth)
      }
    }

    handleInitialization()

    window.addEventListener("resize", handleResize)
    return () => {
      window.removeEventListener("resize", handleResize)
    }
  }, [fitMode, minZoom, maxZoom, autoFitInitialized, lastContainerWidth, isInitializing])

  const scaleValue = zoom / 100

  // If hiding UI, render children directly without any wrapper or controls
  if (hideUI) {
    return (
      <ZoomContext.Provider value={{ zoom: 100, scaleValue: 1, hideUI: true }}>
        <div className={className}>{children}</div>
      </ZoomContext.Provider>
    )
  }

  return (
    <ZoomContext.Provider value={{ zoom, scaleValue, hideUI: false }}>
      <div ref={containerRef} className={`flex flex-col min-h-0 ${className}`}>
        {/* Global Controls - Unscaled */}
        <div data-uhuu-editor className="fixed right-4 bottom-4 z-50 flex items-center gap-1.5 px-2.5 py-1.5 bg-white/90 backdrop-blur-md border border-gray-200/60 rounded-lg shadow-sm">
          {menuItems}
          <div className="h-4 w-px bg-gray-200 mx-0.5" />
          <DropdownMenu modal={false}>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="sm" title="Zoom" className="text-xs font-medium text-gray-700 hover:bg-gray-100/80 h-7 px-2.5">
                {Math.round(zoom)}%
                <ChevronDown className="w-3 h-3 ml-1 opacity-60" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent className="w-52 p-1.5" align="end">
              <DropdownMenuItem
                onClick={() => handleFitMode("width")}
                className={`cursor-pointer flex items-center ${fitMode === "width" ? "bg-gray-100" : ""}`}
              >
                <UnfoldHorizontal className="w-4 h-4 mr-2" />
                <span>Fit to Width</span>
              </DropdownMenuItem>
              <DropdownMenuItem
                onClick={() => handleFitMode("height")}
                className={`cursor-pointer flex items-center ${fitMode === "height" ? "bg-gray-100" : ""}`}
              >
                <UnfoldVertical className="w-4 h-4 mr-2" />
                <span>Fit to Height</span>
              </DropdownMenuItem>
              <DropdownMenuItem
                onClick={() => handleFitMode("both")}
                className={`cursor-pointer flex items-center ${fitMode === "both" ? "bg-gray-100" : ""}`}
              >
                <Maximize className="w-4 h-4 mr-2" />
                <span>Fit to Page</span>
              </DropdownMenuItem>
              <DropdownMenuSeparator className="my-1.5" />
              
              {/* Zoom Controls - Compact inline layout */}
              <div className="flex items-center justify-center gap-2 px-3 py-2.5" onClick={(e) => e.stopPropagation()}>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={(e) => {
                    e.stopPropagation()
                    zoomOut()
                  }}
                  disabled={zoom <= minZoom}
                  className="h-8 w-8 p-0 hover:bg-gray-100 disabled:opacity-40"
                  title="Zoom out (25%)"
                >
                  <ZoomOut className="w-4 h-4" />
                </Button>
                
                <div className="relative">
                  <input
                    type="number"
                    value={Math.round(zoom)}
                    onChange={(e) => {
                      const value = Number.parseInt(e.target.value)
                      if (!isNaN(value)) {
                        const clampedValue = Math.min(Math.max(value, minZoom), maxZoom)
                        setZoom(clampedValue)
                        setFitMode("none")
                      }
                    }}
                    onFocus={(e) => e.target.select()}
                    className="w-20 pr-6 text-center text-sm text-gray-700 bg-white border border-gray-300 rounded px-2 py-1.5 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                    min={minZoom}
                    max={maxZoom}
                  />
                  <span className="absolute right-2 top-1/2 -translate-y-1/2 text-xs text-gray-400 pointer-events-none">%</span>
                </div>
                
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={(e) => {
                    e.stopPropagation()
                    zoomIn()
                  }}
                  disabled={zoom >= maxZoom}
                  className="h-8 w-8 p-0 hover:bg-gray-100 disabled:opacity-40"
                  title="Zoom in (25%)"
                >
                  <ZoomIn className="w-4 h-4" />
                </Button>
              </div>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>

        {/* Content Container */}
        <div ref={contentRef}>
          <div className={preview === "two_pages" ? "group_two_pages" : "flex flex-col items-center"}>{children}</div>
        </div>

        <div className="flex justify-center pb-16">
          <Button size="sm" variant="outline" onClick={() => (onAddPage ? onAddPage() : null)}>
            <Plus />
          </Button>
        </div>
      </div>
    </ZoomContext.Provider>
  )
}
