import { useContext, useState } from "react";
import { ConfigContext } from '../config/config-provider'; // Import the ConfigContext
import { getDialogProps } from './dialog-props';
import './../styles/uhuu-indicators.css';


interface EditableProps {    
  className?: string;
  dialog?: object;
  children?: React.ReactNode;
}

const Editable = (props: EditableProps) => {
  const config = useContext(ConfigContext);  
  // Get page dimensions and bleed from props or use defaults (from context if not provided)  
  
  return (    
      <div 
        className={props.className} 
        {...getDialogProps(props, config)}
      >
        {props.children}
      </div>    
  );
};

export default Editable;