class Compatibility {

  static handlePageBreakStyles() {
    document?.querySelectorAll(".page-break-after[data-paged-css]").forEach((el) => {
      const pagedSheet = el.closest('div.uhuu-page-sheet');
      const pagedCSS = el.getAttribute('data-paged-css');
      
      if (pagedSheet && pagedCSS) {
        const cssClasses = pagedCSS.split(' ').filter(Boolean);
        cssClasses.forEach((cssClass) => pagedSheet.classList.add(cssClass));
      }
    });
  }
  
  static handleUhuuDialogs() {
    const clickListener = function(this: HTMLElement) {
      const value = JSON.parse(this.getAttribute("data-uhuu") || '{}');
      $uhuu.editDialog(value);
    };
  
    document?.querySelectorAll("[data-uhuu]").forEach((el) => {
      el.removeEventListener('click', clickListener);
      el.addEventListener('click', clickListener);
    });
  }

  static handle() {
    Compatibility.handlePageBreakStyles();
    Compatibility.handleUhuuDialogs();
  }
}

export default Compatibility;