class DocumentUtils {
  static setupPageStyles(printCssUrl?: string) {
    if (!printCssUrl || typeof document === 'undefined') return;
    const link = document.createElement("link");
    link.rel = "stylesheet";
    link.href = printCssUrl;
    document.head.appendChild(link);
    return link;
  }

  static removePageStyles(link: HTMLLinkElement) {
    if (link && typeof document !== 'undefined') document?.head.removeChild(link);
  }
  
}

export default DocumentUtils;