/**
 * Page Group Utilities
 * 
 * Locked schema with explicit types and kind discriminator.
 * All helpers and reducers use these types for consistency.
 */

import type { ComponentType } from 'react';
import type { PageDataBinding } from './data-binding';

export type IntegrationRequirement = 'required' | 'optional' | 'none';

export type IntegrationConfig = {
  mode?: IntegrationRequirement;
};

export type PageComponentProps = {
  integration?: any;
  payload?: any;
  pageId?: string;
  templateId?: string;
  pageNum?: number;
  componentKey?: string;
  page?: StandalonePage;
  pagePayload?: any;
  parentGroup?: PageGroup;
  dataBinding?: PageDataBinding;
};

export type PageComponent = ComponentType<PageComponentProps>;

export type StandalonePage = {
  kind: 'page';
  id: string;                    // Instance ID (unique)
  componentKey?: string;         // Template key for component resolution
  templateId?: string;          // Reference to template (for repeatable pages)
  label?: string;
  repeatable?: boolean;         // Template metadata: whether it can be added again
  maxInstances?: number | null; // Template metadata: max allowed instances
  integration?: IntegrationRequirement | IntegrationConfig;
  pageNum?: number; // Derived, not stored in state
  component?: PageComponent; // Optional component for this page
  [key: string]: any; // Allow additional props
};

export type PageGroup = {
  kind: 'group';
  id: string;                    // Instance ID (unique)
  templateId?: string;          // Reference to template blueprint
  repeatable?: boolean;         // Can this be added multiple times?
  maxInstances?: number | null; // Max instances allowed (null = unlimited)
  integration?: IntegrationRequirement | IntegrationConfig;
  label?: string;
  pages: Omit<StandalonePage, 'pageNum' | 'kind'>[]; // Internal pages (no pageNum, no kind, but can have renderer)
  pageNum?: never; // Groups don't have pageNum
  [key: string]: any; // Allow integration metadata and custom fields
};

export type PageItem = StandalonePage | PageGroup;

// Payload persistence helpers
export const DEFAULT_PAGE_EDITOR_STATE_KEY = 'uhuu_page_editor';

export type PageEditorState = {
  key: string;
  items: PageItem[];
  totalPages: number;
  updatedAt: string;
};

// Page filtering types for conditional rendering
export type RenderMode = 'all' | 'cover' | 'text' | 'custom';

export type PageRange = {
  start: number; // 1-based index, supports negative indices (-1 = last page, -2 = second to last)
  end: number;   // Inclusive
};

export type PageFilterConfig = {
  mode?: RenderMode;
  ranges?: PageRange[]; // Used with 'custom' mode
  coverPageCount?: number; // Number of pages at start/end for 'cover' mode (default: 2)
};

// Type guards (using kind discriminator - much safer than checking for pages property)
export function isPageGroup(item: PageItem): item is PageGroup {
  return item.kind === 'group';
}

/**
 * Flatten items to get all pages with derived pageNum
 * Internal function used by getTotalPageCount
 */
function flattenPages(items: PageItem[]): Array<StandalonePage & { pageNum: number }> {
  const result: Array<StandalonePage & { pageNum: number }> = [];
  let pageNum = 1;
  
  for (const item of items) {
    if (isPageGroup(item)) {
      // Group: add all pages from the group
      for (const page of item.pages) {
        result.push({ 
          ...page, 
          kind: 'page' as const,
          pageNum: pageNum++ 
        } as StandalonePage & { pageNum: number });
      }
    } else {
      // Standalone page
      result.push({ 
        ...item, 
        pageNum: pageNum++ 
      });
    }
  }
  
  return result;
}

/**
 * Calculate and assign pageNum to all pages (central source of truth)
 * Returns items with pageNum derived
 */
export function updatePageNumbers(items: PageItem[]): Array<PageItem & { pageNum?: number }> {
  const result: Array<PageItem & { pageNum?: number }> = [];
  let pageNum = 1;
  
  for (const item of items) {
    if (isPageGroup(item)) {
      // Group: update pages within the group
      const updatedPages = item.pages.map(page => ({
        ...page,
        kind: 'page' as const,
        pageNum: pageNum++,
      }));
      result.push({
        ...item,
        pages: updatedPages
      });
    } else {
      // Standalone page
      result.push({
        ...item,
        pageNum: pageNum++
      });
    }
  }
  
  return result;
}

/**
 * Count all pages including those in groups
 */
export function getTotalPageCount(items: PageItem[]): number {
  return flattenPages(items).length;
}

// Reorder utilities
export type ReorderItem = {
  kind: 'page' | 'group';
  id: string;
  label?: string;
  pageComponent?: PageComponent; // Component for standalone pages
  pageComponentKey?: string; // Component key for standalone pages
  firstPageComponent?: PageComponent; // Component for first page in groups
  firstPageComponentKey?: string; // Component key for first page in groups
  [key: string]: any;
}

/**
 * Transform items for reorder dialog (groups appear as single units)
 */
export function transformItemsForReorder(items: PageItem[]): ReorderItem[] {
  return items.map(item => {
    const strictPosition = (item as any).strictPosition;
    if (isPageGroup(item)) {
      // For groups, create a representation with first page info
      const firstPage = item.pages[0];
      const firstPageComponentKey = firstPage?.componentKey ?? firstPage?.id;
      return {
        kind: 'group' as const,
        id: item.id,
        groupId: item.id,
        firstPageId: firstPage?.id,
        firstPageComponentKey,
        firstPageComponent: firstPage?.component, // Pass component from first page
        pageCount: item.pages.length,
        label: item.label || `${item.id} (${item.pages.length} pages)`,
        strictPosition // Preserve strictPosition
      };
    } else {
      // Standalone page
      const pageComponentKey = item.componentKey ?? item.id;
      return {
        kind: 'page' as const,
        id: item.id,
        label: item.label,
        pageId: item.id,
        pageComponentKey,
        pageLabel: item.label,
        pageNum: item.pageNum,
        pageComponent: item.component, // Pass component from page
        strictPosition // Preserve strictPosition
      };
    }
  });
}

/**
 * Restore group structure after reorder
 */
export function restoreItemsFromReorder(reorderedItems: ReorderItem[], originalItems: PageItem[]): PageItem[] {
  // Create a map of original items by ID
  const originalItemsMap = new Map<string, PageItem>();
  originalItems.forEach(item => {
    originalItemsMap.set(item.id, item);
  });
  
  // Reconstruct items array in the new order
  const restored: PageItem[] = [];
  for (const reorderedItem of reorderedItems) {
    const original = originalItemsMap.get(reorderedItem.id);
    if (original) {
      restored.push(original);
    }
  }
  
  return restored;
}

/**
 * Normalize items to ensure they carry a `kind` discriminator.
 * Safely converts legacy shapes that lack `kind` into the locked schema.
 */
export function normalizePageItems(items: Array<PageItem | Omit<PageItem, 'kind'> & { pages?: any[] }>): PageItem[] {
  return items.map((item) => {
    if ('kind' in item && (item as any).kind) {
      return item as PageItem;
    }

    const maybeGroup = (item as any).pages && Array.isArray((item as any).pages);
    if (maybeGroup) {
      return {
        kind: 'group' as const,
        ...(item as any),
        pages: ((item as any).pages ?? []).map((page: any) => {
          const { kind: _ignored, ...rest } = page || {};
          return {
            kind: 'page' as const,
            ...rest,
          };
        }),
      };
    }

    const { kind: _ignored, ...rest } = item as any;
    return {
      kind: 'page' as const,
      ...rest,
    };
  });
}

/**
 * Build a persistable state object for embedding into payloads.
 * Page numbers are derived and therefore omitted from persisted state.
 */
export function buildPageEditorState(
  items: PageItem[],
  key: string = DEFAULT_PAGE_EDITOR_STATE_KEY
): PageEditorState {
  const normalized = normalizePageItems(items);
  return {
    key,
    items: normalized,
    totalPages: getTotalPageCount(normalized),
    updatedAt: new Date().toISOString(),
  };
}

/**
 * Read persisted state from a payload object.
 * Returns normalized items or null when not present.
 */
export function readPageEditorState(
  payload: any,
  key: string = DEFAULT_PAGE_EDITOR_STATE_KEY
): PageEditorState | null {
  const maybeState = payload?.[key];
  if (!maybeState?.items) return null;
  const normalizedItems = normalizePageItems(maybeState.items as PageItem[]);
  return {
    key,
    items: normalizedItems,
    totalPages: getTotalPageCount(normalizedItems),
    updatedAt: maybeState.updatedAt || new Date().toISOString(),
  };
}

/**
 * Helper to produce a new payload object with the page editor state embedded.
 * Does not mutate the provided payload object.
 */
export function mergePageEditorStateIntoPayload(
  payload: any,
  items: PageItem[],
  key: string = DEFAULT_PAGE_EDITOR_STATE_KEY
) {
  const state = buildPageEditorState(items, key);
  return { ...(payload ?? {}), [key]: state };
}

/**
 * Generate a unique ID for instances
 */
export function generateUID(): string {
  return Math.random()
    .toString(36)
    .slice(2, 11);
}

/**
 * Instantiate a standalone page from a template
 */
export function instantiatePage(
  templateId: string,
  componentKey: string,
  overrides?: { 
    label?: string; 
    repeatable?: boolean;
    maxInstances?: number | null;
    integration?: IntegrationRequirement | IntegrationConfig;
    [key: string]: any;
  }
): StandalonePage {
  const instanceId = overrides?.repeatable ? generateUID() : templateId;

  return {
    kind: 'page',
    id: instanceId,
    componentKey,
    templateId,
    label: overrides?.label,
    repeatable: overrides?.repeatable,
    maxInstances: overrides?.maxInstances,
    ...overrides,
  };
}

/**
 * Instantiate a group from a template
 */
export function instantiateGroup(
  templateId: string,
  componentKeys: string[],
  overrides?: { 
    label?: string;
    repeatable?: boolean;
    maxInstances?: number | null;
    integration?: IntegrationRequirement | IntegrationConfig;
    [key: string]: any;
  }
): PageGroup {
  const instanceId = overrides?.repeatable ? generateUID() : templateId;
  
  return {
    kind: 'group',
    id: instanceId,
    templateId,
    label: overrides?.label,
    repeatable: overrides?.repeatable ?? false,
    maxInstances: overrides?.maxInstances ?? null,
    pages: componentKeys.map((key, idx) => ({
      id: `${instanceId}__${key}__${idx}`,
      componentKey: key,
      templateId: key,
    })),
    ...overrides,
  };
}

/**
 * Resolve a page index (supports negative indices)
 * @param index - Page index (1-based, negative from end)
 * @param totalPages - Total number of pages
 * @returns Resolved 1-based page number
 */
function resolvePageIndex(index: number, totalPages: number): number {
  if (index < 0) {
    // Negative index: -1 = last page, -2 = second to last
    return totalPages + index + 1;
  }
  return index;
}

/**
 * Check if a page number is in any of the specified ranges
 * @param pageNum - 1-based page number
 * @param ranges - Array of page ranges
 * @param totalPages - Total number of pages
 */
function isPageInRanges(pageNum: number, ranges: PageRange[], totalPages: number): boolean {
  for (const range of ranges) {
    const start = resolvePageIndex(range.start, totalPages);
    const end = resolvePageIndex(range.end, totalPages);
    
    if (pageNum >= start && pageNum <= end) {
      return true;
    }
  }
  return false;
}

/**
 * Get page ranges based on render mode
 * @param mode - Render mode
 * @param totalPages - Total number of pages
 * @param coverPageCount - Number of pages at start/end for cover mode
 */
function getRangesForMode(mode: RenderMode, totalPages: number, coverPageCount: number = 2): PageRange[] {
  switch (mode) {
    case 'all':
      return [{ start: 1, end: totalPages }];
    
    case 'cover':
      // First N pages and last N pages
      return [
        { start: 1, end: coverPageCount },
        { start: -coverPageCount, end: -1 }
      ];
    
    case 'text':
      // Everything except first N and last N pages
      if (totalPages <= coverPageCount * 2) {
        // Not enough pages for text mode
        return [];
      }
      return [{ start: coverPageCount + 1, end: -(coverPageCount + 1) }];
    
    case 'custom':
    default:
      return [];
  }
}

/**
 * Filter items based on page filter configuration
 * @param items - Page items to filter
 * @param filterConfig - Filter configuration
 * @returns Filtered items maintaining structure (groups with filtered pages)
 */
export function filterPageItems(items: PageItem[], filterConfig?: PageFilterConfig): PageItem[] {
  // No filter or 'all' mode: return all items
  if (!filterConfig || filterConfig.mode === 'all') {
    return items;
  }

  const totalPages = getTotalPageCount(items);
  const mode = filterConfig.mode ?? 'all';
  const coverPageCount = filterConfig.coverPageCount ?? 2;
  
  // Get ranges to include
  const ranges = mode === 'custom' && filterConfig.ranges 
    ? filterConfig.ranges 
    : getRangesForMode(mode, totalPages, coverPageCount);

  if (ranges.length === 0) {
    return [];
  }

  // Filter items based on their existing page numbers
  const filtered: PageItem[] = [];

  for (const item of items) {
    if (isPageGroup(item)) {
      // Filter pages within the group based on their page numbers
      const filteredPages = item.pages.filter((page) => {
        return page.pageNum && isPageInRanges(page.pageNum, ranges, totalPages);
      });

      // Only include group if it has pages after filtering
      if (filteredPages.length > 0) {
        filtered.push({
          ...item,
          pages: filteredPages
        });
      }
    } else {
      // Standalone page - check if it should be included
      if (item.pageNum && isPageInRanges(item.pageNum, ranges, totalPages)) {
        filtered.push(item);
      }
    }
  }

  return filtered;
}
