import { useState, useMemo, useCallback, useContext } from 'react';
import {
  PageItem,
  StandalonePage,
  PageComponent,
  IntegrationRequirement,
  IntegrationConfig,
  isPageGroup,
  updatePageNumbers,
  getTotalPageCount,
  transformItemsForReorder,
  restoreItemsFromReorder,
  ReorderItem,
  PageEditorState,
  DEFAULT_PAGE_EDITOR_STATE_KEY,
  buildPageEditorState,
  instantiateGroup,
  instantiatePage,
  PageFilterConfig,
  filterPageItems,
} from './page-group-utils';
import { createDefaultRenderThumbnail } from './default-render-thumbnail';
import { TemplateDataContext } from './template-data-provider';

export type AvailablePageItem = {
  kind: 'page';
  id: string;                    // Template ID
  templateId?: string;          // Reference to template
  componentKey?: string;         // Component key for resolution
  label?: string;
  component?: PageComponent;
  meta?: Record<string, any>;
  repeatable?: boolean;
  maxInstances?: number | null;
  integration?: IntegrationRequirement | IntegrationConfig;
  [key: string]: any;
};

export type AvailableGroupItem = {
  kind: 'group';
  id: string;                    // Template ID
  templateId?: string;          // Reference to template
  repeatable?: boolean;          // Can be added multiple times
  maxInstances?: number | null;  // Max instances allowed
  pageComponentKeys?: string[];  // Component keys (not instance IDs)
  label?: string;
  integration?: IntegrationRequirement | IntegrationConfig;
  meta?: Record<string, any>;
  [key: string]: any;
};

export type AvailableItem = AvailablePageItem | AvailableGroupItem;

type PageItemWithNumbers = PageItem & {
  pageNum?: number;
  pages?: Array<StandalonePage & { pageNum: number }>;
};

interface UsePageGroupsOptions {
  // Initial state - what pages/groups come when template first loads
  initialItems: PageItem[];
  
  // Available items that can be added (simple: array of page IDs or group configs)
  availableItems?: AvailableItem[];
  
  // Callbacks (optional for MVP)
  onItemsChange?: (items: PageItem[], state: PageEditorState) => void;
  onStateChange?: (state: PageEditorState) => void;
  
  // NEW: For automatic thumbnail rendering
  pageComponents?: { [pageId: string]: PageComponent };
  payload?: any;
  
  // Page setup dimensions (same as Pagination setup prop) - in millimeters
  setup?: { width: number; height: number };

  // Payload state key (for persistence helpers)
  stateKey?: string;

  // Hook for resolving a draft item before it is committed (integration fetch, async defaults, etc.)
  resolveNewItem?: (draft: PageItem) => Promise<PageItem | null | void> | PageItem | null | void;

  notifyError?: (message: string) => void;
  
  // Page filter for conditional rendering
  pageFilter?: PageFilterConfig;
}

interface UsePageGroupsReturn {
  // State
  items: PageItem[];
  itemsWithPageNum: PageItemWithNumbers[];
  totalPageCount: number;
  
  // Available items (filtered - excludes already used)
  availableItemsToAdd: AvailableItem[];
  
  // CRUD operations (MVP - minimal surface)
  addItem: (item: AvailableItem) => Promise<void>;
  removeItem: (itemId: string) => void;
  updateItemFields: (itemId: string, patch: Partial<PageItem>) => void;
  reorderItems: (newOrder: PageItem[]) => void;
  
  // Add page dialog state
  addDialogOpen: boolean;
  setAddDialogOpen: (open: boolean) => void;
  openAddDialog: () => void;
  
  // Render helper (simple - just pages)
  renderItems: (
    renderPage: (
      page: StandalonePage & { pageNum: number },
      parentItem?: PageItem
    ) => React.ReactNode
  ) => React.ReactNode[];
  
  // Reorder helpers (for PageOrderDialog)
  itemsForReorder: ReorderItem[];
  handleReorder: (reorderedItems: ReorderItem[]) => void;
  
  // Default renderThumbnail (if pageComponents provided)
  defaultRenderThumbnail?: (item: ReorderItem, index: number, isDragging?: boolean) => React.ReactNode;
}

export function usePageGroups(options: UsePageGroupsOptions): UsePageGroupsReturn {
  const {
    initialItems,
    availableItems = [],
    onItemsChange,
    onStateChange,
    pageComponents,
    payload,
    setup,
    stateKey = DEFAULT_PAGE_EDITOR_STATE_KEY,
    resolveNewItem,
    notifyError,
    pageFilter,
  } = options;
  
  const [items, setItems] = useState<PageItem[]>(initialItems);
  const [addDialogOpen, setAddDialogOpen] = useState(false);
  
  // Get TemplateDataProvider context (optional - returns null if not available)
  const templateDataContext = useContext(TemplateDataContext);
  
  // Update items and notify parent
  const updateItems = useCallback((newItems: PageItem[]) => {
    setItems(newItems);
    const state = buildPageEditorState(newItems, stateKey);
    
    // If TemplateDataProvider is available, use it as single source of truth for payload updates
    // This ensures all payload modifications (including reordering) go through the provider
    if (templateDataContext?.mergePageEditorState) {
      templateDataContext.mergePageEditorState(newItems, stateKey);
    }
    
    onStateChange?.(state);
    onItemsChange?.(newItems, state);
  }, [onItemsChange, onStateChange, stateKey, templateDataContext]);
  
  // Count instances by templateId
  const instanceCountsByTemplate = useMemo(() => {
    const counts = new Map<string, number>();
    items.forEach(item => {
      const templateId = item.templateId ?? item.id;
      counts.set(templateId, (counts.get(templateId) ?? 0) + 1);
      if (isPageGroup(item)) {
        item.pages?.forEach((page) => {
          const pageTemplateId = (page as any).templateId ?? page.id;
          counts.set(pageTemplateId, (counts.get(pageTemplateId) ?? 0) + 1);
        });
      }
    });
    return counts;
  }, [items]);
  
  // Filter available items based on repeatability and maxInstances
  const availableItemsToAdd = useMemo(() => {
    return availableItems.filter(item => {
      // Handle page items
      if ((item as AvailablePageItem).kind === 'page') {
        const pageItem = item as AvailablePageItem;
        const templateId = pageItem.templateId ?? pageItem.id;
        const count = instanceCountsByTemplate.get(templateId) ?? 0;
        const isRepeatable = pageItem.repeatable ?? false;
        const maxInstances = pageItem.maxInstances ?? null;

        if (!isRepeatable && count > 0) {
          return false;
        }

        if (maxInstances !== null && count >= maxInstances) {
          return false;
        }

        return true;
      }

      // Handle group items
      const groupItem = item as AvailableGroupItem;
      const templateId = groupItem.templateId ?? groupItem.id;
      const count = instanceCountsByTemplate.get(templateId) ?? 0;
      const isRepeatable = groupItem.repeatable ?? false;
      const maxInstances = groupItem.maxInstances ?? null;

      // If not repeatable and already has an instance, hide it
      if (!isRepeatable && count > 0) {
        return false;
      }

      // If repeatable, check maxInstances limit
      if (isRepeatable && maxInstances !== null && count >= maxInstances) {
        return false;
      }

      return true;
    });
  }, [availableItems, instanceCountsByTemplate]);
  
  // Calculate total page count
  const totalPageCount = useMemo(() => getTotalPageCount(items), [items]);
  
  // Add item (page or group) - creates instance from template
  const addItem = useCallback(async (item: AvailableItem) => {
    const resolveIntegrationMode = (
      integration?: IntegrationRequirement | IntegrationConfig
    ): IntegrationRequirement => {
      if (!integration) return 'none';
      if (typeof integration === 'string') return integration;
      return integration.mode ?? 'optional';
    };

    const createDraftFromAvailable = (available: AvailableItem): PageItem => {
      // Handle page items
      if ((available as AvailablePageItem).kind === 'page') {
        const pageItem = available as AvailablePageItem;
        const templateId = pageItem.templateId ?? pageItem.id;
        const componentKey = pageItem.componentKey ?? pageItem.id;
        return instantiatePage(templateId, componentKey, {
          label: pageItem.label,
          repeatable: pageItem.repeatable,
          maxInstances: pageItem.maxInstances,
          integration: pageItem.integration,
          strictPosition: pageItem.strictPosition,
        });
      }

      // Handle group items
      const groupConfig = available as AvailableGroupItem;
      const templateId = groupConfig.templateId ?? groupConfig.id;
      
      // Component keys define the template makeup for this group
      const componentKeys = groupConfig.pageComponentKeys ?? [];

      return instantiateGroup(templateId, componentKeys, {
        label: groupConfig.label,
        repeatable: groupConfig.repeatable ?? false,
        maxInstances: groupConfig.maxInstances ?? null,
        integration: groupConfig.integration,
      });
    };

    const draft = createDraftFromAvailable(item);
    const debugIntegration = typeof window !== 'undefined'
      && (window as any).$uhuu?.debug;
    let integrationData: any = undefined;

    let resolved: PageItem | null | void = draft;
    if (resolveNewItem) {
      resolved = await resolveNewItem(draft);
    } else {
      const integrationMode = resolveIntegrationMode((draft as any).integration);
      let shouldAbort = false;

      if (debugIntegration) {
        console.debug('[usePageGroups] integration check', {
          id: draft.id,
          kind: (draft as any).kind,
          integration: (draft as any).integration,
          mode: integrationMode,
        });
      }

      if (integrationMode !== 'none' && typeof window !== 'undefined') {
        const requestIntegration = (window as any).$uhuu?.requestIntegration?.bind((window as any).$uhuu);
        if (requestIntegration) {
          if (debugIntegration) {
            console.debug('[usePageGroups] requestIntegration start', { id: draft.id, mode: integrationMode });
          }
          integrationData = await requestIntegration({ item: draft, mode: integrationMode });
          if (debugIntegration) {
            console.debug('[usePageGroups] requestIntegration done', { id: draft.id, hasData: integrationData != null });
          }
          if (integrationData == null && integrationMode === 'required') {
            shouldAbort = true;
          }
        } else if (integrationMode === 'required') {
          console.warn('[usePageGroups] Integration data required but $uhuu.requestIntegration is not available.');
          shouldAbort = true;
        }
      }

      if (shouldAbort) return;

    }

    if (resolved === null) return;
    const finalItem = (resolved ?? draft) as PageItem;
    
    // Store integration data using the instance ID
    // For groups, this is the group ID; for pages, it's the page ID
    if (integrationData !== undefined && templateDataContext?.setIntegrationPayload) {
      const instanceId = finalItem.id;
      if (debugIntegration) {
        console.debug('[usePageGroups] storing integration data', {
          instanceId,
          hasData: integrationData != null,
          dataKeys: integrationData ? Object.keys(integrationData) : [],
        });
      }
      templateDataContext.setIntegrationPayload(instanceId, integrationData);
    }
    
    // Insert item in correct position based on strictPosition flags
    const insertItemInPosition = (newItems: PageItem[], itemToInsert: PageItem): PageItem[] => {
      const itemPosition = (itemToInsert as any).strictPosition;
      
      // If the new item has a strictPosition flag, insert it accordingly
      if (itemPosition === 'start') {
        return [itemToInsert, ...newItems];
      } else if (itemPosition === 'end') {
        return [...newItems, itemToInsert];
      }
      
      // Otherwise, find the insertion point:
      // - After all items with strictPosition: 'start'
      // - Before all items with strictPosition: 'end'
      const startItems: PageItem[] = [];
      const middleItems: PageItem[] = [];
      const endItems: PageItem[] = [];
      
      newItems.forEach(item => {
        const position = (item as any).strictPosition;
        if (position === 'start') {
          startItems.push(item);
        } else if (position === 'end') {
          endItems.push(item);
        } else {
          middleItems.push(item);
        }
      });
      
      // Insert new item in the middle section
      return [...startItems, ...middleItems, itemToInsert, ...endItems];
    };
    
    updateItems(insertItemInPosition(items, finalItem));
  }, [items, updateItems, resolveNewItem, templateDataContext]);
  
  // Remove item or page from group
  const removeItem = useCallback((itemId: string) => {
    const showError = (message: string) => {
      if (notifyError) {
        notifyError(message);
      } else {
        alert(message);
      }
    };

    // First, try to find an item with this ID (standalone page or group)
    const targetItem = items.find(i => i.id === itemId);
    
    // If found as a direct item (group or standalone page), remove it entirely
    if (targetItem) {
      const totalCount = getTotalPageCount(items);
      if (totalCount <= 1) {
        showError('Cannot remove the last page. At least one page is required.');
        return;
      }
      
      // Remove integration data if it exists for this instance
      if (templateDataContext?.removeIntegrationPayload) {
        const instanceId = targetItem.id;
        const hasIntegrationData = templateDataContext.payload?.integrations?.[instanceId] !== undefined;
        if (hasIntegrationData) {
          templateDataContext.removeIntegrationPayload(instanceId);
        }
      }
      updateItems(items.filter(item => item.id !== itemId));
      return;
    }
    
    // If not found as direct item, it might be a page ID within a group
    for (const item of items) {
      if (isPageGroup(item) && item.pages.some(p => p.id === itemId)) {
        const totalCount = getTotalPageCount(items);
        if (totalCount <= 1) {
          showError('Cannot remove the last page. At least one page is required.');
          return;
        }
        
        if (item.pages.length === 1) {
          // Last page in group - remove the entire group
          // Remove integration data if it exists for this group instance
          if (templateDataContext?.removeIntegrationPayload) {
            const instanceId = item.id;
            const hasIntegrationData = templateDataContext.payload?.integrations?.[instanceId] !== undefined;
            if (hasIntegrationData) {
              templateDataContext.removeIntegrationPayload(instanceId);
            }
          }
          updateItems(items.filter(i => i.id !== item.id));
        } else {
          // Multiple pages in group - remove just this page from the group
          // Note: We don't remove integration data here because the group still exists
          // and other pages in the group may still use the integration data
          updateItems(items.map(i => {
            if (i.id === item.id && isPageGroup(i)) {
              return {
                ...i,
                pages: i.pages.filter(p => p.id !== itemId),
              };
            }
            return i;
          }));
        }
        return;
      }
    }
  }, [items, notifyError, updateItems, templateDataContext]);
  
  const updateItemFields = useCallback((itemId: string, patch: Partial<PageItem>) => {
    updateItems(items.map(item => {
      if (item.id === itemId) {
        if (isPageGroup(item)) {
          return {
            ...item,
            ...patch,
          };
        }
        return { ...item, ...patch };
      }
      return item;
    }));
  }, [items, updateItems]);
  
  // Reorder items
  const reorderItems = useCallback((newOrder: PageItem[]) => {
    updateItems(newOrder);
  }, [updateItems]);
  
  // Items with derived pageNum for rendering
  // Apply page filtering if configured
  const itemsWithPageNum = useMemo(() => {
    const itemsWithNumbers = updatePageNumbers(items) as PageItemWithNumbers[];
    
    // Apply page filter if configured (preserves original page numbers)
    if (pageFilter) {
      return filterPageItems(itemsWithNumbers as PageItem[], pageFilter) as PageItemWithNumbers[];
    }
    
    return itemsWithNumbers;
  }, [items, pageFilter]);
  
  // Render helper - automatically handles pages vs groups
  const renderItems = useCallback((
    renderPage: (
      page: StandalonePage & { pageNum: number },
      parentItem?: PageItem
    ) => React.ReactNode
  ): React.ReactNode[] => {
    const rendered: React.ReactNode[] = [];
    itemsWithPageNum.forEach(item => {
      if (isPageGroup(item)) {
        const groupPages = (item.pages ?? []) as Array<StandalonePage & { pageNum: number }>;
        groupPages.forEach(page => {
          rendered.push(renderPage(page, item));
        });
      } else {
        rendered.push(renderPage(item as StandalonePage & { pageNum: number }, item));
      }
    });
    return rendered;
  }, [itemsWithPageNum]);
  
  // Reorder helpers
  const itemsForReorder = useMemo(
    () => transformItemsForReorder(itemsWithPageNum as PageItem[]),
    [itemsWithPageNum]
  );
  
  const handleReorder = useCallback((reorderedItems: ReorderItem[]) => {
    const restored = restoreItemsFromReorder(reorderedItems, items);
    
    // Enforce strictPosition constraints: items with strictPosition: 'start' stay at start,
    // items with strictPosition: 'end' stay at end
    const enforcePositions = (itemsToOrder: PageItem[]): PageItem[] => {
      const startItems: PageItem[] = [];
      const middleItems: PageItem[] = [];
      const endItems: PageItem[] = [];
      
      itemsToOrder.forEach(item => {
        const position = (item as any).strictPosition;
        if (position === 'start') {
          startItems.push(item);
        } else if (position === 'end') {
          endItems.push(item);
        } else {
          middleItems.push(item);
        }
      });
      
      return [...startItems, ...middleItems, ...endItems];
    };
    
    updateItems(enforcePositions(restored));
  }, [items, updateItems]);
  
  const openAddDialog = useCallback(() => {
    setAddDialogOpen(true);
  }, []);
  
  // Generate defaultRenderThumbnail if pageComponents provided
  const defaultRenderThumbnail = useMemo(() => {
    if (pageComponents) {
      return createDefaultRenderThumbnail({ pageComponents, payload, setup });
    }
    return undefined;
  }, [pageComponents, payload, setup]);
  
  return {
    items,
    itemsWithPageNum,
    totalPageCount,
    availableItemsToAdd,
    addItem,
    removeItem,
    updateItemFields,
    reorderItems,
    addDialogOpen,
    setAddDialogOpen,
    openAddDialog,
    renderItems,
    itemsForReorder,
    handleReorder,
    defaultRenderThumbnail,
  };
}
