import { isPageGroup, PageItem, StandalonePage } from './page-group-utils';
import {
  buildIntegrationPath,
  resolveIntegrationBinding,
  resolveIntegrationInstanceId,
} from './integration-utils';

type PathBuilder = (subPath?: string) => string | null;

export type IntegrationBinding = {
  instanceId: string | null;
  data?: any;
  path: PathBuilder;
};

export type PageDataBinding = {
  payload: any;
  pageId: string;
  pagePayload?: any;
  parentGroupId?: string;
  integration: IntegrationBinding;
  paths: {
    integration: PathBuilder;
    page: PathBuilder;
    group: PathBuilder;
    document: PathBuilder;
  };
  defaults: {
    imageGalleryPath?: string | null;
  };
};

type BuildPageDataBindingOptions = {
  payload: any;
  page: StandalonePage;
  parentGroup?: PageItem;
  pagePayload?: any;
  defaults?: {
    imageGalleryPath?: string;
  };
};

function buildPath(base: string | null, subPath?: string) {
  if (!base) return null;
  if (!subPath) return base;
  return `${base}.${subPath}`;
}

function resolveGalleryPath(
  payload: any,
  integrationData: any,
  defaults?: { imageGalleryPath?: string }
): string | null | undefined {
  return (
    integrationData?.meta?.imageGalleryPath ??
    integrationData?.config?.imageGalleryPath ??
    integrationData?.imageGalleryPath ??
    payload?.options?.imageGalleryPath ??
    payload?.templateSetup?.options?.imageGalleryPath ??
    defaults?.imageGalleryPath
  );
}

export function buildPageDataBinding({
  payload,
  page,
  parentGroup,
  pagePayload,
  defaults,
}: BuildPageDataBindingOptions): PageDataBinding {
  const integrationBinding = resolveIntegrationBinding(payload, page, parentGroup);
  const groupId = parentGroup && isPageGroup(parentGroup) ? parentGroup.id : undefined;
  const pageBase = `pages.${page.id}`;
  const groupBase = groupId ? `pages.${groupId}` : null;

  return {
    payload,
    pageId: page.id,
    pagePayload,
    parentGroupId: groupId,
    integration: {
      instanceId: integrationBinding.instanceId,
      data: integrationBinding.integration,
      path: (subPath?: string) =>
        buildIntegrationPath(integrationBinding.instanceId, subPath),
    },
    paths: {
      integration: (subPath?: string) =>
        buildIntegrationPath(integrationBinding.instanceId, subPath),
      page: (subPath?: string) => buildPath(pageBase, subPath),
      group: (subPath?: string) => buildPath(groupBase, subPath),
      document: (subPath?: string) => subPath ?? null,
    },
    defaults: {
      imageGalleryPath: resolveGalleryPath(
        payload,
        integrationBinding.integration,
        defaults
      ),
    },
  };
}

export function buildIntegrationDialogPath(
  page: StandalonePage,
  parentGroup?: PageItem,
  subPath?: string
) {
  const instanceId = resolveIntegrationInstanceId(page, parentGroup);
  return buildIntegrationPath(instanceId, subPath);
}
