import * as React from "react"
import { createContext, useContext, useState } from "react"

interface InteractiveModeContextType {
  interactive: boolean
  setInteractive: (value: boolean) => void
}

const InteractiveModeContext = createContext<InteractiveModeContextType>({
  interactive: true,
  setInteractive: () => {},
})

/**
 * Hook to access interactive mode state
 * Returns true if UI should show interactive elements (default), false to hide them
 */
export function useInteractive() {
  return useContext(InteractiveModeContext)
}

/**
 * Hook to check if UI elements should be hidden
 * Returns true when interactive mode is OFF
 */
export function useShouldHideUI(): boolean {
  const { interactive } = useInteractive()
  return !interactive
}

interface InteractiveModeProviderProps {
  children: React.ReactNode
  defaultInteractive?: boolean
}

/**
 * InteractiveModeProvider - Global control for showing/hiding UI elements
 * 
 * When interactive=true (default): Shows all controls (edit mode)
 * When interactive=false: Hides all UI elements (Print Mode)
 * 
 * @example
 * ```tsx
 * <InteractiveModeProvider defaultInteractive={true}>
 *   <MyApp />
 * </InteractiveModeProvider>
 * 
 * // Inside components:
 * const { interactive, setInteractive } = useInteractive()
 * <button onClick={() => setInteractive(!interactive)}>
 *   {interactive ? 'Hide UI' : 'Show UI'}
 * </button>
 * ```
 */
export function InteractiveModeProvider({
  children,
  defaultInteractive = true,
}: InteractiveModeProviderProps) {
  const shouldDisableInteractive = typeof window !== 'undefined' && (window as any)?.$uhuu_renderer ? true : false;
  
  const effectiveDefaultInteractive = shouldDisableInteractive 
    ? false 
    : defaultInteractive
  
  const [interactive, setInteractive] = useState(effectiveDefaultInteractive)

  return (
    <InteractiveModeContext.Provider value={{ interactive, setInteractive }}>
      {children}
    </InteractiveModeContext.Provider>
  )
}




