import { useState, useContext } from "react";
import { ConfigContext } from '../config/config-provider'; // Import the ConfigContext
import { getDialogProps } from '../editable/dialog-props';
import './image.css';

interface ImageSpreadProps {  
  src?: string;
  side?: "start" | "end";
  backgroundColor?: string;
  width?: number;
  left?: number;
  right?: number;
  top?: number;
  bottom?: number;
  height?: number;
  pageWidth?: number;
  pageHeight?: number;
  bleed?: number;
  dialog?: object;
  children?: React.ReactNode;
}

// Constants
export default function ImageSpread(props: ImageSpreadProps) {

  const config = useContext(ConfigContext);  
  // Get page dimensions and bleed from props or use defaults (from context if not provided)
  const BLEED = props.bleed ?? config?.page?.bleed ?? 0;  
  const PAGE_CONTENT_WIDTH = (props.pageWidth ?? config?.page?.width ?? 210);
  const PAGE_CONTENT_HEIGHT = (props.pageHeight ?? config?.page?.height ?? 297);
  
  const {
    src,
    side,
    backgroundColor,
    width: imageW,
    height: imageH,
    left: imageL = 0,
    right: imageR = 0,
    top: imageT = 0,
    bottom: imageB = 0,        
  } = props;

  const [coverClass, setCoverClass] = useState("h-full w-full object-cover object-center");


  const unitFormat = (val: number): string => {
    return `${val}mm`;
  };

  const calculateImageWidth = (): number => {
    let result = imageW; 
    if(!imageW) {
      result = 2 * PAGE_CONTENT_WIDTH;
      if(!imageL) result += 2 * BLEED;
      if(!imageR) result += 2 * BLEED;
      if(imageL || imageR) result -= (imageL ?? 0) + (imageR ?? 0);
    }
    return result;
  };

  const calculateImageHeight = (): number => {
    let result = imageH; 
    if(!imageH) {
      result = PAGE_CONTENT_HEIGHT;
      if(!imageT) result += BLEED;
      if(!imageB) result += BLEED;
      if(imageT || imageB) result -= (imageT ?? 0) + (imageB ?? 0);
    }
    return result;
  };

  // Calculate dimensions  
  const imageWidth = calculateImageWidth();
  const imageHeight = calculateImageHeight();  
  

  const formatStyleValue = (value: number | undefined): string | undefined => 
    value !== undefined ? unitFormat(value) : undefined;

  const createStyle = (styles: Record<string, string | undefined>) => {
    return Object.fromEntries(
      Object.entries(styles).filter(([_, value]) => value !== undefined)
    );
  };

  // const imageTop = imageT ?? (-1 * BLEED);
  // const imageBottom = imageB ?? undefined;
  const imageLeft = imageL > 0 ? imageL : (-1 * BLEED);
  const rightPageOffset = -1 * PAGE_CONTENT_WIDTH + imageLeft; // Offset of right page
  
  const leftStyle = createStyle({
    backgroundColor,
    width: formatStyleValue(imageWidth),
    height: formatStyleValue(imageHeight),
    left: formatStyleValue(imageLeft),
    top: formatStyleValue(imageT > 0 ? imageT + BLEED : imageT),
    bottom: formatStyleValue(imageB > 0 ? imageB + BLEED : imageB)
  });

  const rightStyle = createStyle({
    width: formatStyleValue(imageWidth),
    height: formatStyleValue(imageHeight),
    left: formatStyleValue(rightPageOffset),
    top: formatStyleValue(imageT > 0 ? imageT + BLEED : imageT),
    bottom: formatStyleValue(imageB > 0 ? imageB + BLEED : imageB)
  });

  // calc ratio of image: if imageHeight set to 0 (means it should be "auto") then calc using default pageHeight and spacings
  const ratio =
    imageHeight != 0
      ? imageWidth / imageHeight
      : imageWidth / (PAGE_CONTENT_HEIGHT - (imageT ?? 0) - (imageB ?? 0));

  const imageLoaded = (e) => {
    // Compare loaded image ratio with container ratio to determine cover mode
    const imageRatio = e.target.naturalWidth / e.target.naturalHeight; // Use naturalWidth/Height to get actual image dimensions
    
    // If image is wider relative to container, use vertical cover mode
    // Otherwise use horizontal cover mode
    let imageClass = imageRatio > ratio  ? "cover-vertical"  : "cover-horizontal";    
    setCoverClass(imageClass);
  };


  return <div className={`uhuu-image-container`} style={side == "end" ? rightStyle : leftStyle}>
        <div className="uhuu-image-inner" {...getDialogProps(props, config)}>
          {props.children || (
            <img 
              className={coverClass} 
              src={src} 
              onLoad={(e) => imageLoaded(e)}
              key={`${src}-${imageWidth}-${imageHeight}`} // Force reload when height or width change
            />
          )}
        </div>
      </div>
}

