import * as React from "react"
import { useContext } from "react"
import { MoreHorizontal } from "lucide-react"
import { ConfigContext } from "../config/config-provider"
import { getDialogProps } from "../editable/dialog-props"
import { useShouldHideUI } from "../editor-shell/interactive-mode-context"
import { Button } from "../ui/button"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "../ui/dropdown-menu"
import { cn } from "../ui/cn"

type OptionSelectEvent = Event | React.SyntheticEvent

export type ImageOption = {
  id: string
  label: string
  icon?: React.ReactNode
  dialog?: Record<string, any> | null
  onSelect?: (event: OptionSelectEvent) => void
  disabled?: boolean
}

type ImageRenderProps = React.ImgHTMLAttributes<HTMLImageElement>

interface ImageWithOptionsProps {
  src?: string
  alt?: string
  className?: string
  imageClassName?: string
  style?: React.CSSProperties
  imageStyle?: React.CSSProperties
  overlaySvg?: string
  overlayClassName?: string
  showOverlay?: "always" | "export"
  options?: ImageOption[]
  dialog?: Record<string, any> | null
  dialogProps?: Record<string, any>
  placeholder?: React.ReactNode
  children?: React.ReactNode
  imageProps?: React.ImgHTMLAttributes<HTMLImageElement>
  renderImage?: (props: ImageRenderProps) => React.ReactNode
}

const ImageWithOptions = ({
  src,
  alt = "",
  className,
  imageClassName,
  style,
  imageStyle,
  overlaySvg,
  overlayClassName,
  showOverlay = "export",
  options = [],
  dialog,
  dialogProps,
  placeholder,
  children,
  imageProps,
  renderImage,
}: ImageWithOptionsProps) => {
  const config = useContext(ConfigContext)
  const shouldHideUI = useShouldHideUI()
  const baseDialogProps = dialog ? getDialogProps({ dialog }, config) : {}
  const showOptions = options.length > 0 && !shouldHideUI
  const shouldShowOverlay =
    !!overlaySvg && (showOverlay === "always" || (showOverlay === "export" && shouldHideUI))

  const mergedDialogProps = React.useMemo(() => {
    if (!dialogProps) return baseDialogProps
    const merged: Record<string, any> = { ...baseDialogProps, ...dialogProps }
    if (baseDialogProps.className || dialogProps.className) {
      merged.className = cn(baseDialogProps.className, dialogProps.className)
    }
    Object.keys(baseDialogProps).forEach((key) => {
      const baseHandler = baseDialogProps[key]
      const extraHandler = dialogProps[key]
      if (key.startsWith("on") && typeof baseHandler === "function" && typeof extraHandler === "function") {
        merged[key] = (event: any) => {
          baseHandler(event)
          extraHandler(event)
        }
      }
    })
    return merged
  }, [baseDialogProps, dialogProps])

  const handleOptionSelect = (event: OptionSelectEvent, option: ImageOption) => {
    event.stopPropagation()

    if (option.onSelect) {
      option.onSelect(event)
      return
    }

    if (option.dialog && typeof window !== "undefined") {
      ;(window as any).$uhuu?.editDialog?.(option.dialog)
    }
  }

  const renderOverlay = () => {
    if (!shouldShowOverlay || !overlaySvg) return null
    const trimmed = overlaySvg.trim()
    const isInlineSvg = trimmed.startsWith("<")

    if (isInlineSvg) {
      const normalizedSvg = trimmed.replace(/<svg\b([^>]*)>/i, (match, attrs) => {
        let nextAttrs = attrs
        if (/\bwidth=/.test(nextAttrs)) {
          nextAttrs = nextAttrs.replace(/\bwidth=(\"[^\"]*\"|'[^']*'|[^\s>]+)/i, 'width="100%"')
        } else {
          nextAttrs += ' width="100%"'
        }
        if (/\bheight=/.test(nextAttrs)) {
          nextAttrs = nextAttrs.replace(/\bheight=(\"[^\"]*\"|'[^']*'|[^\s>]+)/i, 'height="100%"')
        } else {
          nextAttrs += ' height="100%"'
        }
        if (/\bpreserveAspectRatio=/.test(nextAttrs)) {
          nextAttrs = nextAttrs.replace(
            /\bpreserveAspectRatio=(\"[^\"]*\"|'[^']*'|[^\s>]+)/i,
            'preserveAspectRatio="xMidYMid slice"'
          )
        } else {
          nextAttrs += ' preserveAspectRatio="xMidYMid slice"'
        }
        return `<svg${nextAttrs}>`
      })
      return (
        <div
          className={cn("pointer-events-none absolute inset-0 z-10", overlayClassName)}
          aria-hidden="true"
          dangerouslySetInnerHTML={{ __html: normalizedSvg }}
        />
      )
    }

    return (
      <img
        src={overlaySvg}
        alt=""
        aria-hidden="true"
        className={cn(
          "pointer-events-none absolute inset-0 z-10 h-full w-full object-cover",
          overlayClassName
        )}
      />
    )
  }

  const renderImageContent = () => {
    const imagePropsClassName = imageProps?.className
    const imagePropsStyle = imageProps?.style
    const finalSrc = imageProps?.src ?? src
    const finalAlt = imageProps?.alt ?? alt
    const mergedImageProps: React.ImgHTMLAttributes<HTMLImageElement> = {
      ...imageProps,
      src: finalSrc,
      alt: finalAlt,
      className: cn("h-full w-full object-cover", imageClassName, imagePropsClassName),
      style: { ...imageStyle, ...imagePropsStyle },
    }

    if (renderImage) {
      return renderImage(mergedImageProps)
    }

    if (finalSrc) {
      return <img {...mergedImageProps} />
    }

    return placeholder ?? null
  }

  return (
    <div className={cn("relative", className)} style={style}>
      <div className="relative h-full w-full" {...mergedDialogProps}>
        {renderImageContent()}
        {children}
        {renderOverlay()}
      </div>
      {showOptions && (
        <div className="absolute right-2 top-2 z-20">
          <DropdownMenu modal={false}>
            <DropdownMenuTrigger asChild>
              <Button
                variant="secondary"
                size="icon"
                title="Image options"
                className="h-7 w-7 shadow-sm"
                onPointerDown={(event) => event.stopPropagation()}
                onClick={(event) => event.stopPropagation()}
              >
                <MoreHorizontal className="h-4 w-4" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent className="w-40 p-1.5" align="end">
              {options.map((option) => (
                <DropdownMenuItem
                  key={option.id}
                  onSelect={(event) => handleOptionSelect(event, option)}
                  disabled={option.disabled}
                >
                  {option.icon && <span className="mr-2 inline-flex">{option.icon}</span>}
                  <span>{option.label}</span>
                </DropdownMenuItem>
              ))}
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      )}
    </div>
  )
}

export default ImageWithOptions
