import {
  PageItem,
  PageGroup,
  StandalonePage,
  instantiateGroup,
  instantiatePage,
  PageComponent,
  IntegrationRequirement,
  IntegrationConfig,
} from './page-group-utils';
import { AvailableItem } from './use-page-groups';

type SimplePageRef = string | (Partial<StandalonePage> & { id: string; componentKey?: string });

type PageTemplateDefinition = {
  label?: string;
  componentKey?: string;
  component?: PageComponent;
  repeatable?: boolean;
  maxInstances?: number | null;
  integration?: IntegrationRequirement | IntegrationConfig;
  allowAsSinglePage?: boolean; // If false, page can only be added as part of a group (default: true)
  strictPosition?: 'start' | 'end'; // If set, page should always stay at the start or end of the document
};

type SimpleGroupDefinition = {
  id: string;                    // Template ID
  label?: string;
  repeatable?: boolean;          // Can be added multiple times
  maxInstances?: number | null;  // Max instances (null = unlimited)
  pageComponentKeys?: string[];  // Template keys (not instance IDs)
  integration?: IntegrationRequirement | IntegrationConfig;
};

type BuildPageGroupsConfigArgs = {
  initial: Array<SimplePageRef | SimpleGroupDefinition | string>;
  groups?: SimpleGroupDefinition[] | Record<string, SimpleGroupDefinition>;
  pageComponentKeys?: string[];
  pages?: Record<string, PageTemplateDefinition>;
  pageComponents?: Record<string, PageComponent>;
};

type BuildPageGroupsConfigResult = {
  initialItems: PageItem[];
  availableItems: AvailableItem[];
  pageComponents: Record<string, PageComponent>;
};

const asPage = (
  page: SimplePageRef, 
  templateId?: string, 
  isInitial: boolean = false,
  templateLookup?: Record<string, PageTemplateDefinition>
): StandalonePage => {
  const pageId = typeof page === 'string' ? page : page.id;
  const templateConfig = templateLookup?.[pageId];
  const componentKey = typeof page === 'string'
    ? (templateConfig?.componentKey ?? pageId)
    : (page.componentKey ?? templateConfig?.componentKey ?? page.id);
  const pageTemplateId = templateId ?? pageId;
  const repeatable = (typeof page === 'string' ? undefined : (page as any).repeatable) ?? templateConfig?.repeatable ?? false;
  const maxInstances = (typeof page === 'string' ? undefined : (page as any).maxInstances) ?? templateConfig?.maxInstances ?? null;
  const label = (typeof page === 'string' ? undefined : page.label) ?? templateConfig?.label;
  const component = (typeof page === 'string' ? undefined : (page as any).component) ?? templateConfig?.component;
  const integration = (typeof page === 'string' ? undefined : (page as any).integration) ?? templateConfig?.integration;
  const strictPosition = (typeof page === 'string' ? undefined : (page as any).strictPosition) ?? templateConfig?.strictPosition;

  if (isInitial) {
    // For initial items with explicit IDs, use the ID directly (don't instantiate)
    return {
      kind: 'page',
      id: pageId,
      componentKey,
      templateId: pageTemplateId,
      label,
      repeatable,
      maxInstances,
      integration,
      component,
      strictPosition,
      ...(typeof page === 'string' ? {} : page),
    };
  }

  // For items added later, instantiate with unique ID
  return instantiatePage(pageTemplateId, componentKey, {
    label,
    repeatable,
    maxInstances,
    integration,
    component,
    strictPosition,
    ...(typeof page === 'string' ? {} : page),
  });
};

const asGroup = (group: SimpleGroupDefinition, isInitial: boolean = false): PageGroup => {
  const componentKeys = group.pageComponentKeys ?? [];
  
  // For initial items, use the template ID directly (don't instantiate)
  // For items added later, instantiate with unique IDs
  if (isInitial) {
    const instanceId = group.id; // Use template ID as instance ID for initial items
    return {
      kind: 'group',
      id: instanceId,
      templateId: group.id,
      label: group.label,
      repeatable: group.repeatable ?? false,
      maxInstances: group.maxInstances ?? null,
      integration: group.integration,
      pages: componentKeys.map((key, idx) => ({
        id: `${instanceId}__${key}__${idx}`,
        componentKey: key,
        templateId: key,
      })),
    };
  }
  
  return instantiateGroup(group.id, componentKeys, {
    label: group.label,
    repeatable: group.repeatable ?? false,
    maxInstances: group.maxInstances ?? null,
    integration: group.integration,
  });
};

const normalizeGroups = (groups?: SimpleGroupDefinition[] | Record<string, SimpleGroupDefinition>): SimpleGroupDefinition[] => {
  if (!groups) return [];
  if (Array.isArray(groups)) return groups;
  return Object.entries(groups).map(([id, def]) => ({ ...def, id }));
};

export const buildPageGroupsConfig = (args: BuildPageGroupsConfigArgs): BuildPageGroupsConfigResult => {
  const {
    initial,
    groups,
    pageComponentKeys = [],
    pages = {},
    pageComponents: explicitPageComponents = {},
  } = args;

  const normalizedGroups = normalizeGroups(groups);
  const groupMap = new Map<string, SimpleGroupDefinition>();
  normalizedGroups.forEach((group) => groupMap.set(group.id, group));
  const inferredPageComponentKeys = pageComponentKeys.length ? pageComponentKeys : Object.keys(pages);
  const mergedPageComponents: Record<string, PageComponent> = { ...explicitPageComponents };
  Object.entries(pages).forEach(([key, def]) => {
    if (def.component) mergedPageComponents[key] = def.component;
  });

  const initialItems: PageItem[] = initial.map((item) => {
    if (typeof item === 'string') {
      const maybeGroup = groupMap.get(item);
      if (maybeGroup) {
        return asGroup(maybeGroup, true); // isInitial = true
      }
      return asPage(item, undefined, true, pages); // isInitial = true
    }

    const maybeGroup = (item as any).pageComponentKeys && Array.isArray((item as any).pageComponentKeys);
    if (maybeGroup) {
      return asGroup(item as SimpleGroupDefinition, true); // isInitial = true
    }

    return asPage(item as SimplePageRef, undefined, true, pages); // isInitial = true
  });

  const availableGroupItems: AvailableItem[] = normalizedGroups.map((group) => {
    const componentKeys = group.pageComponentKeys ?? [];
    return {
      kind: 'group',
      id: group.id, // Template ID
      templateId: group.id,
      label: group.label,
      pageComponentKeys: componentKeys,
      repeatable: group.repeatable ?? false,
      maxInstances: group.maxInstances ?? null,
      integration: group.integration,
    };
  });

  // For standalone pages, create template items
  // Filter out pages that are not allowed as single pages (allowAsSinglePage === false)
  const availablePageItems: AvailableItem[] = inferredPageComponentKeys
    .filter(key => {
      const templateConfig = pages?.[key];
      // Default to true if not specified
      return templateConfig?.allowAsSinglePage !== false;
    })
    .map(key => {
      const templateConfig = pages?.[key];
      return {
        kind: 'page',
        id: key, // Template ID
        templateId: key,
        componentKey: templateConfig?.componentKey ?? key,
        label: templateConfig?.label,
        repeatable: templateConfig?.repeatable ?? false,
        maxInstances: templateConfig?.maxInstances ?? null,
        integration: templateConfig?.integration,
        strictPosition: templateConfig?.strictPosition,
      };
    });

  const availableItems: AvailableItem[] = [
    ...availablePageItems,
    ...availableGroupItems,
  ];

  return { initialItems, availableItems, pageComponents: mergedPageComponents };
};
