import * as React from "react"
import { useState, useEffect } from "react"
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  type DragEndEvent,
  type DragStartEvent,
  DragOverlay,
} from "@dnd-kit/core"
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  rectSortingStrategy,
} from "@dnd-kit/sortable"
import { useSortable } from "@dnd-kit/sortable"
import { CSS } from "@dnd-kit/utilities"

interface SortableItemProps<T> {
  item: T
  index: number
  renderItem: (item: T, index: number, isDragging?: boolean) => React.ReactNode
  renderDragIndicator?: (item: T, index: number) => React.ReactNode
  keyExtractor: (item: T) => string
  disabled?: boolean
}

function SortableItem<T>({
  item,
  index,
  renderItem,
  renderDragIndicator,
  keyExtractor,
  disabled = false,
}: SortableItemProps<T>) {
  const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({
    id: keyExtractor(item),
    disabled,
  })

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
  }

  return (
    <div ref={setNodeRef} style={style} className={`relative group/drag-item ${isDragging ? "opacity-50" : ""} ${disabled ? "opacity-60" : ""}`}>
      {/* Main item content */}
      {renderItem(item, index, isDragging)}

      {/* Drag indicator overlay */}
      {!disabled && (
        renderDragIndicator ? (
          <div {...attributes} {...listeners}>
            {renderDragIndicator(item, index)}
          </div>
        ) : (
          /* If no drag indicator, make entire item draggable */
          <div
            {...attributes}
            {...listeners}
            className="absolute inset-0 cursor-grab active:cursor-grabbing touch-none"
          />
        )
      )}
    </div>
  )
}

function DragOverlayItem<T>({
  item,
  index,
  renderItem,
}: {
  item: T
  index: number
  renderItem: (item: T, index: number, isDragging?: boolean) => React.ReactNode
}) {
  return <div className="rotate-2">{renderItem(item, index, true)}</div>
}

export interface DragDropGridProps<T> {
  items: T[]
  onChange: (reorderedItems: T[]) => void
  renderItem: (item: T, index: number, isDragging?: boolean) => React.ReactNode
  renderDragIndicator?: (item: T, index: number) => React.ReactNode
  keyExtractor: (item: T) => string
  gridColsClass?: string
  className?: string
  renderToolbar?: () => React.ReactNode
  renderEmptyState?: () => React.ReactNode
  showDebugInfo?: boolean
  renderDragOverlay?: (item: T, index: number) => React.ReactNode
  isItemDisabled?: (item: T) => boolean
  canDropAt?: (draggedItem: T, targetIndex: number, items: T[]) => boolean
}

export default function DragDropGrid<T>({
  items: initialItems,
  onChange,
  renderItem,
  renderDragIndicator,
  keyExtractor,
  gridColsClass = "grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-6 2xl:grid-cols-6 gap-4",
  className = "",
  renderToolbar,
  renderEmptyState,
  showDebugInfo = false,
  renderDragOverlay,
  isItemDisabled,
  canDropAt,
}: DragDropGridProps<T>) {
  const [items, setItems] = useState<T[]>(initialItems)

  // Sync with parent state when initialItems changes
  useEffect(() => {
    setItems(initialItems)
  }, [initialItems])

  const [activeId, setActiveId] = useState<string | null>(null)

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  )

  const handleDragStart = (event: DragStartEvent) => {
    const draggedItem = items.find((item) => keyExtractor(item) === event.active.id)
    // Prevent dragging disabled items
    if (draggedItem && isItemDisabled && isItemDisabled(draggedItem)) {
      return
    }
    setActiveId(event.active.id as string)
  }

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event

    if (!over || active.id === over.id) {
      setActiveId(null)
      return
    }

    const draggedItem = items.find((item) => keyExtractor(item) === active.id)
    const oldIndex = items.findIndex((item) => keyExtractor(item) === active.id)
    const newIndex = items.findIndex((item) => keyExtractor(item) === over.id)

    // Prevent dragging disabled items
    if (draggedItem && isItemDisabled && isItemDisabled(draggedItem)) {
      setActiveId(null)
      return
    }

    // Check if drop is allowed at target position
    if (canDropAt && !canDropAt(draggedItem!, newIndex, items)) {
      setActiveId(null)
      return
    }

    if (oldIndex !== -1 && newIndex !== -1) {
      const newItems = arrayMove(items, oldIndex, newIndex)
      setItems(newItems)
      onChange(newItems)
    }

    setActiveId(null)
  }

  const activeItem = items.find((item) => keyExtractor(item) === activeId)
  const activeIndex = activeItem ? items.findIndex((item) => keyExtractor(item) === activeId) : -1

  return (
    <div className={`w-full ${className}`}>
      {renderToolbar && <div className="mb-6">{renderToolbar()}</div>}

      {items.length === 0 && renderEmptyState ? (
        renderEmptyState()
      ) : (
        <div className="mb-6">
          <DndContext
            sensors={sensors}
            collisionDetection={closestCenter}
            onDragStart={handleDragStart}
            onDragEnd={handleDragEnd}
          >
            <SortableContext items={items.map(keyExtractor)} strategy={rectSortingStrategy}>
              <div className={gridColsClass}>
                {items.map((item, index) => (
                  <SortableItem
                    key={keyExtractor(item)}
                    item={item}
                    index={index}
                    renderItem={renderItem}
                    renderDragIndicator={renderDragIndicator}
                    keyExtractor={keyExtractor}
                    disabled={isItemDisabled ? isItemDisabled(item) : false}
                  />
                ))}
              </div>
            </SortableContext>
            <DragOverlay>
              {activeItem ? (
                renderDragOverlay ? (
                  <div className="rotate-2 shadow-lg">{renderDragOverlay(activeItem, activeIndex)}</div>
                ) : (
                  <DragOverlayItem item={activeItem} index={activeIndex} renderItem={renderItem} />
                )
              ) : null}
            </DragOverlay>
          </DndContext>
        </div>
      )}

      {/* Debug info */}
      {showDebugInfo && (
        <div className="fixed top-4 left-4 bg-white rounded-lg border shadow-lg p-3 text-sm max-w-xs">
          <div className="font-medium mb-1">Debug Info</div>
          <div className="text-gray-600 text-xs">
            Items: {items.length} | Active: {activeId || "none"}
          </div>
          <div className="text-xs text-gray-500 mt-1 break-all">
            Order: {items.map((item, i) => `${i + 1}:${keyExtractor(item).slice(0, 3)}`).join("  ")}
          </div>
        </div>
      )}
    </div>
  )
}

