import { useState, forwardRef, useRef, useImperativeHandle, useCallback } from 'react';
import { Previewer } from 'pagedjs';
import debounce from 'lodash/debounce';

const PagedComponent = forwardRef(({ children }, ref) => {
  const [rendering, setRendering] = useState(false);
  const containerRef = useRef(null);
  const pageFlowRef = useRef(null);

  if (typeof document === 'undefined') {
    return null;
  }

  // Expose layout method to parent via ref
  useImperativeHandle(ref, () => ({
    layout: layoutPage,
  }));

  // Memoize the layoutPage function with debounce
  const layoutPage = useCallback(
    debounce(() => {
      // Ensure container and flow elements are available
      if (!containerRef.current || !pageFlowRef.current) {
        console.warn("Container or page flow reference is missing.");
        return;
      }

      setRendering(true);

      const { clientHeight: height, clientWidth: width } = containerRef.current;

      // Clear the container before rendering new content
      containerRef.current.innerHTML = '';

      // Set fixed width and height to avoid layout issues during rendering
      if (height) containerRef.current.style.minHeight = `${height}px`;
      if (width && window !== window.parent) containerRef.current.style.minWidth = `${width}px`;

      // Remove previously inserted paged.js styles
      document.querySelectorAll("style[data-pagedjs-inserted-styles]").forEach((style) => style.remove());

      let pageFileName = "page.css"
      const paged = new Previewer();

      // Wrap in a try-catch to handle errors during preview generation
      try {
        paged.preview(pageFlowRef.current.innerHTML, [pageFileName], containerRef.current).then(() => {
          setRendering(false);

          // revert back container minWidth, minHeight after rendering.
          containerRef.current.style.minWidth = 'auto';
          containerRef.current.style.minHeight = 'auto';

          // Attach event listeners for elements with "data-uhuu" attribute
          document.querySelectorAll("[data-uhuu]").forEach((item) => {
            const clickListener = function () {
              const value = JSON.parse(this.getAttribute("data-uhuu"));
              $uhuu.editDialog(value);
            };
            // Ensure unique listener attachment
            item.removeEventListener('click', clickListener);
            item.addEventListener('click', clickListener);
          });

          // Apply custom classes based on page-break-after elements
          document.querySelectorAll(".page-break-after[data-paged-css]").forEach((el) => {
            const pagedSheet = el.closest('div.pagedjs_sheet');
            const pagedCSS = el.getAttribute('data-paged-css');
            if (pagedSheet && pagedCSS) {
              pagedCSS.split(' ').filter(Boolean).forEach((cssClass) => pagedSheet.classList.add(cssClass));
            }
          });
        });
      } catch (error) {
        console.error("Error during Paged.js preview rendering:", error);
        setRendering(false);
      }
    }, 500),
    []
  );

  return (
    <div className={rendering ? 'rendering uhuu-page' : 'rendered uhuu-page'}>
      {/* Container for Paged.js preview */}
      <div className="pagedjs_preview_container" ref={containerRef}></div>

      {/* Hidden flow container for the content */}
      <div style={{display:"none"}} ref={pageFlowRef}>
        {children}
      </div>
    </div>
  );
});

export default PagedComponent;